<template>
	<!--
    	作者：2443611475@qq.com
    	时间：2018-03-20
    	描述：打赏记录
    -->
	<div style="width: 100%;height:13.33rem;position: relative;z-index: 9992;background: white;">
		<div style="width: 100%;height: 0.88rem;position: fixed;top:0;left: 0;z-index: 9992;line-height: 0.88rem;border-bottom: 0.005rem solid #999999;background: white;">
			<img @touchstart="hui" style="width: 0.4rem;height: 0.4rem;float:left;margin:0.25rem 0.3rem;" src="../../../assets/img/zuo.png" alt="" />
			<p style="font-size: 0.36rem;margin-left: 2rem;float: left;">打赏记录</p>

		</div>
		<div style="height: 0.88rem;"></div>

		<!--
        	作者：2443611475@qq.com
        	时间：2018-03-20
        	描述：循环创建
        -->
		<div class="test_box" v-for="msg in msgs">
			<div style="width: 4.4rem;height: 100%;float: left;position: relative;">
				<div style="width: 100%;height: 0.56rem;margin-top: 0.3rem;line-height: 0.56rem;">
					<img style="width:0.56rem;height:0.56rem;float:left;border-radius:50%;" :src="msg.opPic" />
					<p style="font-size: 0.28rem;float: left;margin-left: 0.2rem;color: #B1B1B1;">{{msg.nickname}}</p>
				</div>

				<span class="ps">
			   	<p>打赏了</p>
			   	<p style="color: #FF9A2B;">{{msg.giftWorth}}个觅币</p>
			   </span>

				<p style="font-size: 0.2rem;color: #B1B1B1;position: absolute;bottom: 0.3rem;left: 0.75rem;">{{msg.insertDate}}</p>
			</div>
			<div style="width: 2.7rem;height: 100%;float: left;">
				<img style="width: 1.4rem;height: 1.4rem;margin: 0.6rem;" :src="msg.picUrl" />
			</div>
		</div>

	</div>
</template>

<script>
	import store from '../../../vuex/store'
	import md5 from 'js-md5';
	export default {
		store,
		data() {
			return {
				msgs: []
			}
		},
		methods: {
			hui() {
					this.$router.push({
						path: '../message/news'
						});
				
			},
			request_data() {

				/*处理成后台想要的请求数据*/
				if(this.$store.state.rewards.length > 0) {
					var str = "";

					if(this.$store.state.rewards.length > 1) {
						for(var i = 0; i < this.$store.state.rewards.length; i++) {
							if(i == this.$store.state.rewards.length - 1) {
								str += this.$store.state.rewards[i]
							} else {
								str += this.$store.state.rewards[i] + ","
							}

						}

					} else {
						str = this.$store.state.rewards[0]
					}

					//console.log(str,"id")
					if(str != "") {
						var url = this.$store.state.request_url + "/api/map/user/artUserMessagePush.do";
						var memId =this.$store.state.data.memId;
						var ts = new Date().getTime();
						var token = this.$store.state.data.tokEn;
						var sign = md5(url + memId + token + ts);
						/*请求打赏记录*/
						this.$http({
							url: this.$store.state.request_url + '/api/map/user/artUserMessagePush.do?memId='+memId+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								"sIgn":sign
							},
							body: {
								fldrId: str,
								type: "reward"
							},
							emulateJSON: false,
						}).then(function(response) {

							if(response.body.meta.res === "00000") {
								console.log(response.body);
								this.msgs = response.body.data;
							}
						})
					}

				}

			}

		},
		created() {
			console.log(this.$store.state.rewards, "打赏Id")
			this.request_data(); //处理并请求打赏数据

			//this.msgs=this.$store.state.rewards;
		}
	}
</script>

<style scoped="scoped">
	.chat_box {
		width: 100%;
		height: 1.7rem;
		margin-top: 0.02rem;
	}
	
	.test_box {
		width: 95%;
		height: 2.5rem;
		float: right;
		border-bottom: 0.005rem solid #CACACA;
	}
	
	.ps {
		font-size: 0.3rem;
		float: left;
		margin-left: 0.75rem;
		margin-top: 0.3rem;
	}
	
	.ps p {
		float: left;
	}
</style>


