<template>
	<div style="width: 100%;position: relative;z-index:300;background:white;height: 13.33rem;">
		<div style="width: 90%;position: relative;margin: 0 auto;">
			<span @touchstart="hui" style="font-size: 0.4rem;">
			<img style="width: 0.3rem;margin-right: 0.1rem;margin-top: 0.4rem;" src="../../assets/img/zuo.png" alt="" />返回</span>
			<!--闲情发布 -->

			<!--标题 -->
			<h6 style="font-size: 0.7rem;">
		     	闲情发布</h6>
			<p style="font-size: 0.3rem;color: #D0D0D0;margin-top: 0.1rem;">
				上传作品并填写资料</p>

			<img v-for="(i,index) in $store.state.img_xianqing" style="margin-right: 0.2rem;margin-top: 0.2rem;" class="zhu" :src="i.picUrl" />
			<div style="float:left;margin-top: 0.2rem;">
				<img class="zhu" src="../../assets/img/drawablefffff/icon_announce_lifeImg.png" />
				<input style="opacity:0;margin-left: -2.5rem;" class="zhu" ref="ss" name="file" type="file" accept="image/png,image/gif,image/jpeg" @change="uploadpic_tos" />
			</div>

			<div style="margin-top: 0.2rem;border-top: 0.01rem solid #2C3E50;border-bottom: 0.01rem solid #2C3E50;float: left;" class="boxs">
				<!--<span><p style="font-size: 0.3rem;float: left;">选择可见</p>
		     	  </span>-->
				<mu-icon-menu icon="more_vert" @change="handleChange" :open="open" @open="handleOpen" @close="handleClose" :value="value">
					<mu-menu-item class='menu' value="1" title="朋友圈可见" />
					<mu-menu-item class='menu' value="2" title="所有人可见" />
				</mu-icon-menu>

				<mu-raised-button class='kejian' label="选择可见" @click="toggle" />

				<p style="font-size:0.26rem;position:absolute;left:2.3rem;top:0;">{{kj}}</p>

				<i style="float: right;font-size: 0.45rem;line-height: 1rem;margin-right: 0rem;color: #CACACA;" class="fa fa-chevron-right"></i>
			</div>

			<textarea v-model="$store.state.xq_box.neirong" style="width: 100%;height:5.5rem;font-size: 0.3rem;margin-top: 0.1rem;border: none;" placeholder="说点什么新的生活动态..." maxlength="512"></textarea>

		</div>
		<div @click="fabu" style="width:100%;height: 1.3rem;box-shadow:0px 0px  15px 0.1px #CACACA;position:fixed;bottom:0rem;">
			<button style="width:6.4rem;height:0.9rem; background:rgba(46,49,53,1);border-radius: 0.15rem;color: white;position: absolute;left: 0.5rem;top: 0.2rem;font-size: 0.3rem;">
				上传作品
			</button>
		</div>
	</div>
</template>

<script>
	import store from '../../vuex/store.js'
	import { Toast } from 'mint-ui';
	import md5 from 'js-md5';
	export default {
		store,
		data() {
			return {
				value: '3',
				kj: '朋友圈可见',
				open: false,
				supplementary_pic:"cover_pic"
			}
		},
		methods: {
			open3() {
				this.$notify({
					title: '成功',
					message: '闲情发布成功',
					type: 'success',
		//        duration:0,
				});
			},
			handleChange(val) {
				this.value = val
				if(this.value == 1) {
					this.$store.state.xq_box.kejian = 'friends'
					this.kj = '朋友圈可见'
				} else if(this.value == 2) {
					this.$store.state.xq_box.kejian = 'all'
					this.kj = '所有人可见'
				}
			},
			toggle() {
				this.open = !this.open
			},
			handleOpen() {
				this.open = true
			},
			handleClose() {
				this.open = false
			},

			hui() {
				this.$store.state.home_tai = 3
				 this.$router.push({
								path: '../add_facth'
							});
				
			},
			uploadpic_tos(e) { //上传图事件
				if(this.$store.state.img_xianqing.length < 8) {
					var url="http://www.ding-new.com:8080/a10/api/mapManager/uploadPic.do";
		           var id=this.$store.state.data.memId;
		           var token=this.$store.state.data.tokEn;
		           var ts=new Date().getTime();
		           var sign=md5(url+id+token+ts)
					var self = this
					let file = e.target.files[0]
					/* eslint-disable no-undef */
					let param = new FormData() // 创建form对象
					param.append('file', file) // 通过append向form对象添加数据
					param.append('plateName', 'leisure') // 添加form表单中其他数据
					param.append('picType', this.supplementary_pic)
					console.log(param.get('file')) // FormData私有类对象，访问不到，可以通过get判断值是否传进去
					
					if(file) {
						// 添加请求头
//						this.$axios.post('api/mapManager/uploadPic.do', param, config)
//							.then(response => {
//								if(response.data.meta.res === '00000') {
//									this.$store.state.img_xianqing.push(response.data.data)
//									
//									if(this.$store.state.img_xianqing.length!=0){
//                         	               this.supplementary_pic="supplementary_pic"
//                                  }
//									
//									console.log(this.$store.state.img_xianqing)
//								}
//							})
                           this.$http({
					
                     url:"http://www.ding-new.com:8080/a10/api/mapManager/uploadPic.do?memId="+id+"&ts="+ts,
					method: 'post',
					headers: {
						'Content-Type': 'multipart/form-data',
						'sIgn': sign
					},
					body: param,
					emulateJSON: false,
				}).then(function(response) {
					if(response.body.meta.res == "00000") {
						
						this.$store.state.img_xianqing.push(response.data.data)
//									
									if(this.$store.state.img_xianqing.length!=0){
                           	               this.supplementary_pic="supplementary_pic"
                                    }
									
									console.log(this.$store.state.img_xianqing)
						
					}
				})

                         
					}
				}
			},

			fabu() {
				console.log(this.$store.state.xq_box)
              if(this.$store.state.img_xianqing.length>0){
              	  var url="http://www.ding-new.com:8080/a10/api/mapManager/saveArticle.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign=md5(url+id+token+ts)
              	
				this.$http({
					url: "http://www.ding-new.com:8080/a10/api/mapManager/saveArticle.do?memId="+id+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						'sIgn': sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId: this.$store.state.data.memId,
						picUrl: this.$store.state.img_xianqing,
						artTitle: '1',
						docUrl: this.$store.state.xq_box.neirong,
						artFlag: '2',
						tags: '1',
						visibleRange: this.$store.state.xq_box.kejian
					}
				}).then(function(response) {
					console.log(response)
					if(response.body.meta.res == '00000') {
						console.log(response.body)
						
						this.$store.state.img_xianqing = []
						this.$store.state.xq_box.neirong = ''
						this.open3()
						this.$router.push({
								path: '../slider'
							});
						
					}
				}).catch(function(err) {
					console.log(err)
				})
				}else {
					Toast({
											message: '至少需要上传一张配图',
											position: 'middle',
											duration: 2000
										});
				}
			}

		}
	}
</script>

<style scoped="scoped">
	* {
		outline: none;
	}
	
	.boxs {
		width: 100%;
		height: 1rem;
		margin-top: 1.2rem;
		line-height: 1rem;
		position: relative;
	}
	
	.zhu {
		width: 2.5rem;
		height: 2.5rem;
		float: left;
	}
	
	.kejian {
		width: 2rem;
		height: 0.8rem;
		font-size: 0.3rem;
		margin-top: 0.1rem;
		float: left;
	}
	
	.menu {
		width: 3rem;
		height: 1rem;
		font-size: 0.3rem;
	}
</style>


