<template>
	<div style="width: 100%;margin-bottom:1.35rem;position:relative;z-index: 300;background: white;">
		<!--
        	作者：2443611475@qq.com
        	时间：2018-04-12
        	描述：我的闲情
        -->
		<div style="width: 100%;position:relative;z-index: 300;background: white;">
			<div class="box_top" style="width: 100%;height: 0.88rem;background: white;position: fixed;top: 0;left: 0;z-index: 99;line-height: 0.88rem;">
				<img @click="hui" style="width: 0.4rem;height: 0.4rem;float: left;margin:0.25rem 0.3rem;" src="../../assets/img/zuo.png" alt="" />
				<p style="font-size: 0.36rem;margin-left: 3.1rem;">我的闲情</p>
			</div>

			<div style="height: 0.88rem;">

			</div>
			<div v-for="(i,index) in data" class="boxs">

				<div class="chat_box">
				<span style="line-height:1.2rem;margin-left: 0.3rem;float: left;" >
					<p style="float: left;font-size: 0.4rem;font-weight: 600;">{{i.insertDate.split(' ')[0].split("-")[2]}}日</p>
					<p style="float: left;font-size: 0.25rem;margin-left: 0.1rem;font-weight: 600;">{{i.insertDate.split(' ')[0].split("-")[1]}}月</p>
				</span>
				</div>
      <div @click="dainji(i)">
				<!--三张图 -->
				<div class="img_box" v-if="i.picInfo.length == 3">
					<!--大图容器-->
					<div style="width:5rem;height:5rem;float:left;vertical-align:middle;text-align: center;overflow: hidden;">
						<img :src="i.picInfo[0].picUrl" style="height:100%;max-width:auto;" />
					</div>

					<div style="width: 2.44rem;height: 5rem;float: left;margin-left:0.04rem;">
						<!--小图容器上面-->
						<div style="width: 100%;height: 2.47rem;vertical-align:middle;text-align: center;overflow: hidden;">
							<img :src="i.picInfo[1].picUrl" style="height:100%;max-width:auto;" />
						</div>
						<!--小图容器下面-->
						<div style="width: 100%;height: 2.48rem;margin-top:0.04rem;">
							<img :src="i.picInfo[2].picUrl" style="height:100%;max-width:auto;" />
						</div>
					</div>
				</div>

				<!--二张图 -->
				<div class="img_box" v-else-if="i.picInfo.length==2">

					<div style="width: 100%;height: 5rem;">
						<!--小图容器上面-->
						<div style="width:49.5%;height:100%;margin-right:0.25%;float:left;vertical-align:middle;text-align: center;overflow: hidden;">
							<img :src="i.picInfo[0].picUrl" style="height:100%;max-width:auto;" />
						</div>
						<!--小图容器下面-->
						<div style="width:49.5%;height:100%;margin-left:0.25%;float:right;vertical-align:middle;text-align: center;overflow: hidden;">
							<img :src="i.picInfo[1].picUrl" style="height:100%;max-width:auto;" />
						</div>
					</div>
				</div>

				<!--一张图 -->
				<!--<div  >-->

					<div v-else-if="i.picInfo.length==1" style="height:5rem;width:100%;vertical-align:middle;text-align:center;overflow: hidden;">
						<img :src="i.picInfo[0].picUrl" style="max-height:auto;width:100%;" />
					</div>
				<!--</div>-->
				<!--文本容器-->
				<div style="width: 100%;box-sizing: border-box;padding: 0.37rem;">
					<!--文本内容-->

					<p class="text_span">
						{{i.docUrl}}
					</p>
				</div>
             </div>	
				<div class="boxs_to">

					<!--<span style="font-size: 0.4rem;float: left;margin: 0.15rem auto;color: #CACACA;" @click="callTap(i,index)">
				
				<i style="margin-left: 0.02rem;" class="fa fa-heart-o" v-for="c in callStauts">
					<img v-if="c.id==i.artId&&c.stauts=='1'" src="../../static/img/icon_detail_good_normal.png"/>
	        		<img v-else-if="c.id==i.artId&&c.stauts=='2'" src="../../static/img/icon_detail_good_active.png"/>
	        	</i><br />
				<p style="font-size: 0.22rem;">点赞</p>
				</span>-->
					<span style="font-size: 0.4rem;float: right;margin-top: 0.15rem;margin-right: 0.2rem;" @click="del_article(i,index)">
					<i style="margin-left: -0.05rem;color: #CACACA;" class="fa fa-shopping-basket"></i><br />
						<!--<img src="../../assets/img/icon_detail_comment.png"/>-->
					<p style="font-size: 0.22rem;color: #CACACA;">删除</p>
					</span>
					<span style="font-size: 0.4rem;float: right;margin-top: 0.15rem;margin-right: 0.6rem;color: #CACACA;">
					<i style="margin-left: 0.04rem;" class="fa fa-share"></i><br />
						<!--<img src="../../assets/img/icon_detail_share.png"/>-->
					<p style="font-size: 0.22rem;">转发</p>
					</span>

					<!--<span style="font-size: 0.4rem;float: left;margin-top: 0.15rem;margin-left: 0.6rem;color: #CACACA;" @click="replyTap(i)">
				<i style="margin-left: 0.04rem;" class="fa fa-comment-o"><img src="../assets/img/icon_detail_comment.png"/></i><br />
				<p style="font-size: 0.22rem;">评论</p>
				</span>-->

				</div>
				<div style="width: 100%;height: 0.3rem;background: #F6F6F6;"></div>
			</div>
			<div class="more">
				<p style="color: #D0D0D0;font-size: 0.3rem;">----------------------------{{more_text}}----------------------------</p>
			</div>
		</div>

	</div>

</template>

<script>
	import store from '../../vuex/store.js'
	import { Toast } from 'mint-ui';
	import md5 from 'js-md5';
	
	export default {
		store,
		components: {

		},
		data() {
			return {
				data: [],
				
				callStauts: [], //储存点赞状态
				reply: {
					is_reply: false
				},
				call: {
					id: '',
					stauts: ''
				},
				page: 1,
				more_text: '加载更多',
				date:''
			}
		},
		methods: {
			dainji(i){
				console.log(i,'闲情')
				this.$store.state.back_id = 7
				this.$store.state.worksId = i.artId
//				window.location='/#/works_detail'
				this.$router.push({
						path: '../works_detail'
						});
				
			},
			hui() {
//				window.location = '#/myspace'
				this.$router.push({
						path: '../myspace'
						});
			},
			/*获取闲情*/
			getLeisure() {
					var url = this.$store.state.request_url + "/api/map/art/selectFlag.do";
					var memId =this.$store.state.data.memId;
					var ts = new Date().getTime();
					var token = this.$store.state.data.tokEn;
					var sign = md5(url + memId + token + ts);
				this.$http({
					
					
					url: this.$store.state.request_url + "/api/map/art/selectFlag.do?memId="+memId+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						"sIgn":sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId: this.$store.state.data.memId,
						typeId: '2',
						page: this.page,
						rows: 3
					}
				}).then(function(response) {
					
					if(response.body.meta.res == '00000') {

						if(response.body.data.artDocumentsList.length > 0) {
							for(var i = 0; i < response.body.data.artDocumentsList.length; i++) {
								this.data.push(response.body.data.artDocumentsList[i])
							}
							this.more_text = "加载更多"
							

						} else if(response.body.data.artDocumentsList.length == 0) {
							
								this.more_text = "我是有底线的"
							
							
						}
					}
				}).catch(function(err) {
					console.log(err)
				})
			},
			/*查询点赞状态*/
			searchCall(item) {
					var url = this.$store.state.request_url + "/api/map/art/likeForOpId.do";
					var memId =this.$store.state.data.memId;
					var ts = new Date().getTime();
					var token = this.$store.state.data.tokEn;
					var sign = md5(url + memId + token + ts);
				this.$http({
					url: this.$store.state.request_url  + "/api/map/art/likeForOpId.do?memId="+memId+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						"sIgn":sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId: this.$store.state.data.memId, //当前用户Id
						attentionOpId: item.opId, //作者Id
						artId: item.artId //作品Id
					}
				}).then(function(response) {

					if(response.body.meta.res == '00000') {
						console.log(response.body.meta.msg, "状态");
						if(response.body.meta.msg == "NO") {
							this.call.id = item.artId;
							this.call.stauts = "1";
							this.callStauts.push(this.call); //未点赞
							this.call = {};
						} else if(response.body.meta.msg == "YES") {
							this.call.id = item.artId;
							this.call.stauts = "2";
							this.callStauts.push(this.call); //已点赞
							this.call = {};
						}

					}
				}).catch(function(err) {
					console.log(err)
				})
			},
			/*点赞事件*/
			callTap(item, i) {
					var url = this.$store.state.request_url + "/api/map/art/artLike.do";
					var memId =this.$store.state.data.memId;
					var ts = new Date().getTime();
					var token = this.$store.state.data.tokEn;
					var sign = md5(url + memId + token + ts);
				this.$http({
					url: this.$store.state.request_url  + "/api/map/art/artLike.do?memId="+memId+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						"sIgn":sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId: item.opId, //作者Id
						likeType: item.artFlag, //类型
						artId: item.artId, //作品Id
						likeOpId: this.$store.state.data.memId //用户Id
					}
				}).then(function(response) {
					
					this.callStauts = [];

					for(var i = 0; i < this.data.length; i++) {
						
						this.searchCall(this.data[i]);
					}

				}).catch(function(err) {
					console.log(err)
				})
			},
			/*评论*/
			replyTap(e) {
				this.$store.state.commentWorksdata = e; //闲情信息，评论用
				this.reply.is_reply = true
			},
			/*删除文章*/
			del_article(item,i) {
					var url = this.$store.state.request_url + "/api/map/art/deleteArt.do";
					var memId =this.$store.state.data.memId;
					var ts = new Date().getTime();
					var token = this.$store.state.data.tokEn;
					var sign = md5(url + memId + token + ts);
				this.$http({
					url: this.$store.state.request_url  + "/api/map/art/deleteArt.do?memId="+memId+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						"sIgn":sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId: item.opId, //作者Id
						artId: item.artId, //作品Id
					}
				}).then(function(response) {
					//console.log("删除");
					
					if(response.body.meta.res == "00000") {
					/*提示语*/
					Toast({
						message: "删除成功",
						position: 'middle',
						duration: 2000
						});
						
					this.data.splice(i, 1);//删除闲情
					
					}
				}).catch(function(err) {
					console.log(err)
				})
			},
			/*加载*/
			moreTap() {
				console.log("加载")
				this.more_text = "加载中"
				this.page += 1;
				this.getLeisure();
			},
			/*监听滚动事件，上滑加载更多*/
			listenScroll() {
				var _that = this;
				$(window).scroll(function() {
					let scrollTop = $(window).scrollTop();
					let windowTop = $(window).height();
					let documentTop = $(document).height();
					if(documentTop - windowTop <= scrollTop) {
						//console.log(_that.more_text)
						//console.log(_that.more_text === "加载更多")
						if(_that.more_text === "加载更多") {
							console.log(11111)
							_that.moreTap();
						}

					}
				});
			}
		},
		mounted() {
			
			this.getLeisure();
			this.listenScroll();

		}
	}
</script>

<style scoped="scoped">
	.boxs {
		width: 100%;
		background: white;
	}
	
	.chat_box {
		width: 100%;
		height: 1.2rem;
		margin-top: 0.02rem;
	}
	
	.imgs {
		width: 1rem;
		height: 1rem;
		margin: 0.1rem 0.25rem;
		border-radius: 50%;
	}
	
	.text_box {
		width: 5.78rem;
		height: 100%;
		float: right;
		position: relative;
		line-height: 1.2rem;
	}
	
	.img_box {
		width: 100%;
		height: 5rem;
	}
	
	.text_span {
		font-size: 0.28rem;
		color: #999999;
		width: 100%;
		/*height: 2.9rem;*/
		 display: -webkit-box;  
                        /*display: -moz-box;  
                        overflow: hidden;  
                        text-overflow: ellipsis;  
                        word-break: break-all;  
                        -webkit-box-orient: vertical;  
                        -webkit-line-clamp:7;*/
		
	}
	
	.boxs_to {
		width: 90%;
		height: 1rem;
		margin: 0.1rem auto;
		position: relative;
		border-top: 0.005rem solid #666666;
	}
	
	.more {
		padding-top: 0.3rem;
		width: 100%;
		height: 0.5rem;
		text-align: center;
		background-color: #fff;
	}
</style>


