<template>
	<div id="app">
		<div class="back" @touchstart="backTo">
			<img src="../../assets/img/back.png" />
			<p>返回</p>
		</div>
		<div class="title">
			<div class="title_left">
				<p class="title_p">基本资料</p>
				<p class="title_tip">请填写基本资料</p>
			</div>
			<div class="title_right">
				<input class="file" style="height: 100%; z-index: 100;position: absolute;top: 0; opacity: 0;" name="file" type="file" accept="image/png,image/gif,image/jpeg" @change="update"/>
					<img v-if="imageUrl" :src="imageUrl" class="avatar" style="position: absolute;top: 0;">
			</div>
		</div>
		<div class="nink">
			<p>昵称</p>
			<input ref="nink" type="text" placeholder="请输入您的用户昵称" maxlength="12"/>

		</div>
		<div class="phone" @click="show_sex">
			<p>性别</p>
			<input ref="sex_str" type="tel" readonly="readonly" placeholder="请选择性别" :value="sex" />
			<img src="../../assets/img/jiantou_down.png" />
		</div>
		<div class="password" @click="show_date_in">
			<p>生日</p>
			<input ref="both" type="text" readonly="readonly" placeholder="请选择出生年月日" :value="date_str" />
			<img src="../../assets/img/jiantou_down.png" />
		</div>
		<div class="loation">
			<p>地区</p>
			<!--<input type="text" readonly="readonly" placeholder="请选择所在地区" />-->
			<Cascader :data="adr" @on-change="changeAdress"></Cascader>
		</div>
		<div class="xieyi">完成注册表示您同意
			<a href="#">《觅友使用协议》</a>
		</div>
		<div class="sumbit" @touchstart="finish">
			<p>完成注册</p>
		</div>

		<mt-datetime-picker :startDate="startDate" :endDate="endDate" v-show="show_date" @cancel="handleCancel" @confirm="handleConfirm" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日">
		</mt-datetime-picker>
		<mt-actionsheet :actions="action_value" v-model="sheetVisible"></mt-actionsheet>
	</div>
</template>
<script src="../../../static/js/adaption.js"></script>
<script src="../../../static/js/swiper.js"></script>

<script>
	import { Toast } from 'mint-ui';
	import store from '../../vuex/store.js'
	import md5 from 'js-md5';
	export default {
		store,
		data() {
			return {
				startDate: new Date('1980/01/01'),
				endDate: new Date(),
				pickerVisible: true,
				sex: '',
				date_str: '',
				is_show: false,
				show_date: false,
				imageUrl: 'static/img/uploadheadimg.png',
				img:'',//临时图片路径,用来储存上传来返回的路径
				sheetVisible: false,
				action_value: [{
						name: '男',
						method: this.getnan
					},
					{
						name: '女',
						method: this.getnv
					}
				],
				adr: [],
				location: '',
				/*注册信息对象，待用*/
				regiter_data:{
					nink:'',
					sex:'',
					both:'',
					location:'',
					img:''
				}
			}
		},
		methods: {
			backTo() {
//				window.location = '/#/regiter'
                this.$router.push({path: '../regiter'})
			},
			finish() {
				var nink = this.$refs.nink.value.trim(); //昵称
				var sex_str = this.$refs.sex_str.value.trim(); //性别
				var both = this.$refs.both.value.trim(); //生日
				
				if(nink != null && nink != "") {
					if(sex_str != null && sex_str != "") {
						if(both != null && both != "") {
							if(this.location != null && this.location != "") {
								//console.log("昵称：" + nink + "  性别：" + sex_str + "  生日：" + both + "  :" + this.location);
								if(this.img!=''){
									this.regiter_data.nink=nink;
									this.regiter_data.sex=sex_str;
									this.regiter_data.both=both;
									this.regiter_data.location=this.location;
									this.regiter_data.img=this.img;
									this.$store.state.regiter_data=this.regiter_data;
									//console.log(this.regiter_data)
//									window.location = '/#/select_fan'
									this.$router.push({path: '../select_fan'})
								}else{
									/*提示语*/
									Toast({
										message: "请上传头像",
										position: 'middle',
										duration: 2000
									});
								}
								
								
								
							} else {
								/*提示语*/
								Toast({
									message: "请选择地区",
									position: 'middle',
									duration: 2000
								});
							}
						} else {
							/*提示语*/
							Toast({
								message: "请选择生日",
								position: 'middle',
								duration: 2000
							});
						}
					} else {
						/*提示语*/
						Toast({
							message: "请选择性别",
							position: 'middle',
							duration: 2000
						});
					}
				} else {
					/*提示语*/
					Toast({
						message: "请填写昵称",
						position: 'middle',
						duration: 2000
					});
				}

			},
			show_sex() {
				this.sheetVisible = true
			},
			getnan() {
				this.sex = "男"
			},
			getnv() {
				this.sex = "女"
			},
			/*选择生日*/
			handleConfirm(e) {
				var d = new Date(e);
				var youWant = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
				console.log(youWant);
				this.date_str = youWant;
				console.log(e);
				this.show_date = false;
			},
			show_date_in() {
				this.show_date = true;
			},
			handleCancel() {
				this.show_date = false;
			},
			/*选择地区*/
			changeAdress(e) {
				this.location = e[0] + "-" + e[1];
				//console.log(e);
			},
			
			//上传头像
			update(e) {
				console.log("进来")
				var url = this.$store.state.request_url + "/api/map/user/uploadOpPic.do";
				var memId =localStorage.memId;
				var ts = new Date().getTime();
				var token = localStorage.tokEn;
				var sign = md5(url + memId + token + ts);
				
				console.log("memId:"+memId+"  ts:"+ts+"  sign:"+sign+"  token:"+token)
				let file = e.target.files[0];
				let param = new FormData(); //创建form对象
				param.append('picName', file); //通过append向form对象添加数据
				param.append('memId', memId); //添加form表单中其他数据
				console.log(param.get('memId')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去

				this.$http({
					url: this.$store.state.request_url + "/api/map/user/uploadOpPic.do?memId="+memId + "&ts="+ts,
					method: 'post',
					headers: {
						'Content-Type': 'multipart/form-data',
						'sIgn': sign
					},
					body: param,
					emulateJSON: false,
				}).then(function(response) {
					if(response.body.meta.res == "00000") {

						this.imageUrl = response.body.data.sourcePicUrl; //用来显示
						this.img = response.body.data.sourcePicUrl; //用来传递
					}

				})
			}		

		},
		mounted() {
			this.adr = this.$store.state.adr
		}

	}
</script>

<style scoped="scoped">
	#app {
		/*overflow-x: hidden;*/
		width: 100%;height: 13.34rem;
		position: relative;z-index: 300;
	    background: white;
		
	}
	
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "微软雅黑";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	
	input,
	textarea {
		outline: none;
	}
	/*返回*/
	
	.back {
		width: 1.8rem;
		height: 1rem;
		margin-left: 0.25rem;
	}
	
	.back img {
		width: 0.5rem;
		height: 0.5rem;
		float: left;
		margin-top: 0.25rem
	}
	
	.back p {
		font-size: 0.4rem;
		margin-left: 0.1rem;
		line-height: 1rem;
		float: left;
	}
	/*标题*/
	
	.title {
		margin-top: 0.5rem;
		width: 100%;
		height: 2rem;
		margin-left: 0.25rem;
	}
	
	.title_p {
		font-size: 0.8rem;
		font-weight: bold;
		color: rgba(51, 51, 51, 1);
		font-family: castellar;
	}
	
	.title_tip {
		font-size: 0.35rem;
		color: #D0D0D0;
	}
	
	.title_left {
		float: left;
	}
	
	.title_right {
		height: 1.6rem;
		width: 22%;
		margin-right: 0.5rem;
		float: right;
		position: relative;
	}
	
	.title_right img {
		width: 100%;
		height: 100%;
		margin: 0 auto;
		border-radius: 50%;
	}
	
	.nink {
		position: absolute;
		top: 3.75rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
		border-bottom: rgba(247, 247, 247, 1) solid 3px;
	}
	
	.nink p {
		width: 25%;
		font-size: 0.4rem;
		float: left;
		line-height: 1rem;
		color: rgba(51, 51, 51, 1);
	}
	
	.nink input {
		width: 60%;
		height: 100%;
		border: 0;
		text-align: left;
		float: left;
		outline: none;
		/*去除默认颜色边框*/
		font-size: 0.32rem;
	}
	
	.phone {
		position: absolute;
		top: 4.75rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
		border-bottom: rgba(247, 247, 247, 1) solid 3px;
	}
	
	.phone p {
		width: 25%;
		float: left;
		font-size: 0.4rem;
		line-height: 1rem;
		font-family: SimHei;
		color: rgba(51, 51, 51, 1);
	}
	
	.phone input {
		width: 60%;
		height: 100%;
		border: 0;
		text-align: left;
		float: left;
		outline: none;
		/*去除默认颜色边框*/
		font-size: 0.32rem;
	}
	
	.phone img {
		width: 10%;
		margin-top: 0.3rem;
		float: right;
	}
	
	input::-webkit-input-placeholder {
		/* WebKit browsers */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input:-moz-placeholder {
		/* Mozilla Firefox 4 to 18 */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input::-moz-placeholder {
		/* Mozilla Firefox 19+ */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input:-ms-input-placeholder {
		/* Internet Explorer 10+ */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	.password {
		position: absolute;
		top: 5.9rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
		border-bottom: rgba(247, 247, 247, 1) solid 3px;
	}
	
	.password p {
		width: 25%;
		float: left;
		line-height: 1rem;
		font-size: 0.425rem;
		color: rgba(51, 51, 51, 1);
	}
	
	.password input {
		width: 60%;
		height: 100%;
		border: 0;
		outline: none;
		/*去除默认颜色边框*/
		text-align: left;
		float: left;
		font-size: 0.32rem;
	}
	
	.password img {
		width: 10%;
		margin-top: 0.3rem;
		float: right;
	}
	
	.loation {
		position: absolute;
		top: 7rem;
		width: 90%;
		left: 0.4rem;
		height: 1.25rem;
		border-bottom: solid 1px #D0D0D0;
	}
	
	.loation p {
		width: 25%;
		float: left;
		font-size: 0.425rem;
		line-height: 1.25rem;
		color: rgba(51, 51, 51, 1);
	}
	
	.loation input {
		width: 60%;
		height: 70%;
		border: 0;
		font-size: 0.5rem;
		margin-top: 0.15rem;
		outline: none;
		/*去除默认颜色边框*/
		float: left;
	}
	
	.loation img {
		width: 10%;
		margin-top: 0.4rem;
		float: right;
	}
	
	.sumbit {
		width: 80%;
		height: 0.75rem;
		border-radius: 0.1rem;
		background-color: #000000;
		position: fixed;
		bottom: 0.5rem;
		margin: 0 10%;
	}
	
	.sumbit p {
		text-align: center;
		color: #fff;
		font-size: 0.375rem;
		margin-top: 0.1rem;
	}
	
	.xieyi {
		width: 80%;
		height: 0.25rem;
		font-size: 0.25rem;
		position: fixed;
		text-align: center;
		bottom: 1.5rem;
		margin: 0 10%;
	}
	
	.xieyi a {
		color: #D0D0D0;
	}
	/*上传头像*/
	
	.avatar-uploader {
		width: 100%;
		height: 100%;
		
	}
	
	.avatar-uploader img {
		padding: 0.05rem;
		width: 1.7rem;
		height: 1.6rem;
	}
	
	.avatar-uploader .el-upload {
		border: 1px dashed #D0D0D0;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	
	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}
	
	.avatar-uploader-icon {
		font-size: 0.5rem;
		color: red;
		width: 1rem;
		height: 1rem;
		line-height: 1.25rem;
		border: 1px solid #D0D0D0;
		text-align: center;
	}
	
	.avatar {
		width: 1rem;
		height: 1rem;
		display: block;
	}
	
	.avatar-uploader i {
		width: 1.25rem;
		height: 1.25rem;
	}
</style>


