<template>
	<div id="app" v-show="d.is_show">
		<div  @click="cancel" style="width: 100%;height: 18.7rem;position:fixed;top: 0;left: 0;background:rgba(0,0,0,0.4);z-index: 10;" >
		
	</div>
		<div v-if="ad"  class="dia">
			<p @click="reply">回复</p>
			<div class="down"></div>
			<p>举报</p>
			<div class="xian" ></div>
			<p @click="cancel">取消</p>
		</div>
		
		<div v-else class="footer">
				<textarea  ref="vas" type="text" :placeholder='"回复@"+$store.state.input_x.disUser+"："'></textarea>
				 <div @click="tower_go" class="send">
					<p>发送</p>
				 </div>
			</div>
		
		
	</div>
	
</template>

<script>
	import store from '../../vuex/store.js'
	import md5 from 'js-md5';
	import { Toast } from 'mint-ui';
	export default {
		store,
		data() {
			return {
					ad:true,
					vas:'',
			}
		},
        props:['d'],
		methods: {
			cancel(){
				this.d.is_show = false
				this.ad = true
			},
			reply(){
				this.ad = false
			},
			
		
			tower_go(){
				
             	var disUser ='';
             	var disUserPic ='';
                 var identification ='';
                 var vas=this.$refs.vas.value;
             	if(vas!=''){
             		var url="http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign=md5(url+id+token+ts)
             		this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do?memId="+id+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								'sIgn': sign
							},
							body: {
								opId:this.$store.state.data.memId,
								
							},
							emulateJSON: false,
						}).then(function(response) {
							 
							if(response.body.meta.res == "00000") {
								console.log(response.body)
								disUser = response.body.data.nickname
								disUserPic = response.body.data.opPic
								identification = response.body.data.idCard
								
								console.log(disUser,'1111111',disUserPic)
                               
                               if(disUser!==''&&disUserPic!==''){
                 	              console.log("评论");
                 	        var url1="http://www.ding-new.com:8080/a10/api/map/art/artDis.do";      
                 	        var sign1=md5(url1+id+token+ts)      
            	  this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/art/artDis.do?memId="+id+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								'sIgn': sign1
							},
							body: {
								disId:this.$store.state.input_x.disId,
								opId:this.$store.state.data.memId,
								disOpId:this.$store.state.works_ads.opId,
								artId:this.$store.state.worksId,
								disContent:vas,
								disUser:disUser,
								disUserPic:disUserPic,
								identification:"vip"
							},
							emulateJSON: false,
						}).then(function(response) {
							  console.log(response.body,"二级回复状态");
							if(response.body.meta.res == "00000") {
								
								Toast({
											message: '回复成功',
											position: 'middle',
											duration: 2000
									});
								this.$refs.vas.value= ''
								this.cancel()
							}
						}).catch(function(err) {
							console.log(err)
						})
						
						}
                 
							}
						}).catch(function(err) {
							console.log(err)
						})
             		
                 
						}else{
							Toast({
											message: '评论内容不能为空',
											position: 'middle',
											duration: 2000
										});
							
						}
						
			},
			
		}
	}
</script>
</script>

<style scoped="scoped">
	input,textarea{
		outline: none;
	}
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "微软雅黑";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	.dia{
		position:fixed;
		bottom: 0;
		z-index: 10;
		height: 3rem;
		background-color: #fff;
		width: 100%;
	}
	.dia p{
		height: 30%;
		line-height: 1rem;
		color:rgba(51,51,51,1);
		font-size: 0.35rem;
		text-align: center;
	}
	.down{
		width: 100%;
		height: 0.01rem;
		margin-top: 0.2rem;
		background:rgba(247,247,247,1);
	}
	.xian{
		height: 0.2rem;
		width: 100%;
		background:rgba(247,247,247,1);
	}
	.footer {
		height: 1rem;
		width: 100%;
		position: fixed;
		bottom: 0;
		z-index: 300;
		-moz-box-shadow: 0 0 0.3rem rgba(247,247,247,1); /* 老的 Firefox */
		box-shadow: 0 0 0.3rem rgba(247,247,247,1);
		background: white;
	}
	.footer textarea {
		outline: none;
		float: left;
		border: 0;
		width: 70%;
		font-size: 0.35rem;
		margin-top: 0.1rem;
		height: 0.8rem;
		margin-left: 3%;
		color: rgba(180, 180, 180, 1);
		border: #D8D8D8 solid 1px;
		line-height: 0.8rem;
		padding-left: 0.2rem;
		opacity:0.01rem;
		border-radius: 0.15rem ; 
	}
	.send {
		width: 18%;
		background-color: #000;
		height: 0.6rem;
		margin-top: 0.2rem;
		margin-right: 0.25rem;
		border-radius: 0.1rem;
		float: right;
	}
	
	.send p {
		color: #fff;
		padding: 0.1rem;
		text-align: center;
		font-size: 0.3rem;
	}
</style>


