<template>
	<!--
    	作者：2443611475@qq.com
    	时间：2018-04-27
    	描述：广场页面
   -->
	<div style="width: 100%;">
		
		<!--
        	作者：2443611475@qq.com
        	时间：2018-04-27
        	描述：顶部第一级导航栏
        -->
       
		<div class="top_on1" style="">
			<span class="pan">
				<i style="font-size:0.4rem;float:left;margin-top:0.4rem;margin-right: 0.2rem;" class="fa fa-map-marker"></i>
				<p style="font-size:0.44rem;">{{dy}}</p>
			</span>
			<span style="margin-left:3.2rem;width:1rem;margin-right:0.1rem;" class="pan">
				<p style="font-size:0.44rem;">社区</p>
			</span>
			<span style="width:1rem;margin: 0;" class="pan">
				<p style="font-size:0.44rem;color:#D5D5D5;">商城</p>
			</span>
		</div>
		 <!--
        	作者：2443611475@qq.com
        	时间：2018-04-27
        	描述：顶部第二级导航栏
        -->
		 <div class="top_no2" style="">
		 	<div @touchstart="js1" style="margin-left: 0.35rem;" :class="{tables:table_biao.noa1,tables_to:table_biao.noe1}">
		 		人气榜
		 	</div>
		 	<div @touchstart="js2" :class="{tables:table_biao.noa2,tables_to:table_biao.noe2}">
		 		朋友圈
		 	</div>
		 	<div @touchstart="js3" :class="{tables:table_biao.noa3,tables_to:table_biao.noe3}">
		 		头条
		 	</div>
		 	<div @touchstart="js4" :class="{tables:table_biao.noa4,tables_to:table_biao.noe4}">
		 		深度
		 	</div>
		 </div> <div style="width: 100%;height: 2.2rem;"></div>
		     
		 
		<div style="width:100%;background:white;padding-bottom: 1rem;">
				<router-view></router-view>
			</div>
			
			
	</div>
</template>

<script>
	import router from '../../router/index.js'
	import {MP} from '../../../static/js/map.js'
	export default{
		router,
		data(){
			return{
                table_biao:{noa1:true,noe1:false,noa2:true,noe2:false,noa3:true,noe3:false,noa4:true,noe4:false,}
			}
		},
		  methods:{
              js1(){//人气榜点击事件
              	this.table_biao.noa1 = false;this.table_biao.noe1 = true;this.table_biao.noa2 = true;this.table_biao.noe2 = false;
                this.table_biao.noa3 = true;this.table_biao.noe3 = false;this.table_biao.noa4 = true;this.table_biao.noe4 = false;
                router.replace({path:'../square/Popularity_list'})
              },
		  	  js2(){//朋友圈点击事件
              	this.table_biao.noa1 = true;this.table_biao.noe1 = false;this.table_biao.noa2 = false;this.table_biao.noe2 = true;
                this.table_biao.noa3 = true;this.table_biao.noe3 = false;this.table_biao.noa4 = true;this.table_biao.noe4 = false;
                router.replace({path:'../square/dynamic'})
		  	  },
              js3(){//头条点击事件
              	this.table_biao.noa3 = false;this.table_biao.noe3 = true;this.table_biao.noa2 = true;this.table_biao.noe2 = false;
                this.table_biao.noa1 = true;this.table_biao.noe1 = false;this.table_biao.noa4 = true;this.table_biao.noe4 = false;
                router.replace({path:'../square/The_headlines'})
              },
              js4(){//深度点击事件
              	this.table_biao.noa4 = false;this.table_biao.noe4 = true;this.table_biao.noa2 = true;this.table_biao.noe2 = false;
                this.table_biao.noa3 = true;this.table_biao.noe3 = false;this.table_biao.noa1 = true;this.table_biao.noe1 = false;
                router.replace({path:'../square/depth'})
              },
              
              collectTap(){
						
			}
              
              
		  },
		  mounted(){
		  	console.log(this.$store.state.tr)
		  	
		  	if(this.$store.state.tr == 1){
		  		this.js3()
		  	}else if(this.$store.state.tr == 2){
		  		this.js4()
		  	}else if(this.$store.state.tr == 0){
		  		this.js1()
		  	}else if(this.$store.state.tr == 4){
		  		this.js2()
		  	}
		  	
//		  	this.collectTap() 
		  	
		  	
		  	
		  },
		  computed:{
		  	   dy(){
		  	   	  return this.$store.state.dy.substring(3,6)
		  	   }
		  }
	}
</script>

<style scoped="scoped">
	.top_on1{
		width:100%;height:1.2rem;line-height:1.2rem;font-weight:600;
		position:fixed;top:0;
		background:white;z-index:300;
	}
	.top_no2{
		width:100%;height:1rem;position:fixed;top: 1.2rem;
		background: white;
		z-index: 300;
	}
	.pan{
		width: 1.5rem;
		float: left;
		margin-left:0.5rem;
	}
	
	.tables{
		font-size: 0.32rem;
		float: left;
		margin-left: 0.7rem;
		
		height: 0.5rem;
		line-height: 0.5rem;
		margin-top: 0.27rem;
	}
	.tables_to{
		font-size: 0.32rem;
		float: left;
		margin-left: 0.7rem;
		background: black;
		color: white;
		height: 0.5rem;
		line-height: 0.5rem;
		margin-top: 0.27rem;
	}
	
</style>


