				</div>
			</div>
			<div class="xian"></div>
			
			<div v-show="noa_s" style="width: 100%;height: 5rem;">
				<img class="img_no" style="" src="../../assets/img/icon_noComment.png"/>
			</div>
			
			<div @click="shows(index)" class="comment_item" v-for="(arr,index) in worksComment">
