			call_btn(){//点赞事件
				console.log(this.works_data,'________asdasd_____________')
				    var url="http://www.ding-new.com:8080/a10/api/map/art/artLike.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign=md5(url+id+token+ts)
					this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/art/artLike.do?memId="+id+"&ts="+ts,
