						}
						}).catch(function(err) {
							console.log(err)
						})
						
					
						
					/*查询用户信息*/
					console.log(response.body.data.opId)
					if(response.body.data.opId) {
						var url2="http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do";
				        var sign2=md5(url2+id+token+ts)
						this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do?memId="+id+"&ts="+ts,
							method: 'post',
