							}
						}).catch(function(err) {
							console.log(err)
						})
					}
					
					/*查询作品评论*/
					if(response.body.data.artId) {
						var url4="http://www.ding-new.com:8080/a10/api/map/art/selectArtDis.do";
				        var sign4=md5(url4+id+token+ts)
						this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/art/selectArtDis.do?memId="+id+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								'sIgn': sign4
							},
							body: {
								artId: response.body.data.artId,
								parentId:0
							},
							emulateJSON: false,
						}).then(function(response) {
							console.log("作品评论");
							console.log(response);
							if(response.body.meta.res == "00000") {
								this.worksComment = response.body.data.artDis;
								if(response.body.data.total == '0'){
									this.noa_s=true
								}else{
									this.noa_s=false
								}
								
							}
						}).catch(function(err) {
							console.log(err)
						})
					}
				}
			}).catch(function(err) {
				console.log(err)
			})
		},
			
		},
		mounted(){
			this.Gdoc()
//			this.acst = this.$store.state.act;
			
//			console.log(this.$store.state.worksId+" : "+this.$store.state.worksName);
		if(this.$store.state.back_id == 3){
			this.xiao =false
		}
		
		
		}

	}
</script>

