<template>
	<!--
    	作者：2443611475@qq.com
    	时间：2018-03-20
    	描述：新的粉丝
    -->
	<div style="width: 100%;height:13.33rem;position: relative;z-index: 9992;background: white;">
		<div style="width: 100%;height: 0.88rem;position: fixed;top:0;left: 0;z-index: 9992;line-height: 0.88rem;border-bottom: 0.005rem solid #999999;background: white;">
			<img @touchstart="hui" style="width: 0.4rem;height: 0.4rem;float:left;margin:0.25rem 0.3rem;" src="../../../assets/img/zuo.png" alt="" />
			<p style="font-size: 0.36rem;margin-left: 3.1rem;">新的粉丝</p>
		</div>
		<div style="height: 0.88rem;"></div>

		<div class="chat_box" v-for="fen in fensi">
			<img class="imgs" style="" :src="fen.opPic" alt="" />
			<div class="text_box">
				<p style="font-size: 0.38rem;margin-top: 0.45rem;margin-left: 0.2rem;">{{fen.nickname}}</p>
				<p style="font-size: 0.24rem;color: #B1B1B1;margin-left: 0.2rem;">{{fen.signature}}</p>
				<div class="biao" @touchstart="attentionTap(fen)">{{attentionText}}</div>
			</div>
		</div>

		<!--<div class="chat_box">
			<img class="imgs" style="" src="../../assets/img/head_img.png" alt="" />
			<div class="text_box">
				<p style="font-size: 0.38rem;margin-top: 0.45rem;margin-left: 0.2rem;">个人ID</p>
				<p style="font-size: 0.24rem;color: #B1B1B1;margin-left: 0.2rem;">个性签名个性签名个性签名</p>
				<div class="biao">+关注</div>
			</div>
		</div>-->
	</div>
</template>

<script>
	import store from '../../../vuex/store'
	import { Toast } from 'mint-ui';
	import md5 from 'js-md5';
	export default {
		store,
		data() {
			return {
				fensi: [],
				attentionText:"+关注"
			}
		},
		methods: {
			hui() {
					this.$router.push({
						path: '../message/news'
						});
				
			},
			request_data() {
				/*处理成后台想要的请求数据*/
				console.log(this.$store.state.fans)
				if(this.$store.state.fans.length > 0) {
					var str = "";
					if(this.$store.state.fans.length > 1) {
						for(var i = 0; i < this.$store.state.fans.length; i++) {
							if(i == this.$store.state.fans.length - 1) {
								str += this.$store.state.fans[i]
							} else {
								str += this.$store.state.fans[i] + ","
							}

						}
						
					} else {
						str = this.$store.state.fans[0]
					}
					
					console.log(str)
					/*请求粉丝记录*/
					if(str != "") {
						var url = this.$store.state.request_url + "/api/map/user/artUserMessagePush.do";
						var memId =this.$store.state.data.memId;
						var ts = new Date().getTime();
						var token = this.$store.state.data.tokEn;
						var sign = md5(url + memId + token + ts);
						this.$http({
							url: this.$store.state.request_url + '/api/map/user/artUserMessagePush.do?memId='+memId+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								"sIgn":sign
							},
							body: {
								fldrId: str,
								type: "fans"
							},
							emulateJSON: false,
						}).then(function(response) {

							if(response.body.meta.res === "00000") {
								console.log(response.body);
								
								this.fensi = response.body.data;
							}
						})
					}
					

				}

			},
			/*关注*/
			attentionTap(e){
				
				var url = this.$store.state.request_url + "/api/map/art/addAttention.do";
				var memId =this.$store.state.data.memId;
				var ts = new Date().getTime();
				var token = this.$store.state.data.tokEn;
				var sign = md5(url + memId + token + ts);
				
				this.$http({
							url: this.$store.state.request_url + '/api/map/art/addAttention.do?memId='+memId+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								"sIgn":sign
							},
							body: {
								opId: this.$store.state.data.memId,
								attentionOpId: e.opId,
								attentionType:"1"
							},
							emulateJSON: false,
						}).then(function(response) {

							if(response.body.meta.res === "00000"||response.body.meta.res === "99999") {
								console.log(response.body);
								/*提示语*/
								Toast({
									message: '关注成功',
									position: 'middle',
									duration: 2000
								});
								this.attentionText="已关注"
							}else{
								/*提示语*/
								Toast({
									message: '关注失败，请重新关注',
									position: 'middle',
									duration: 2000
								});
							}
						})
			}
		},
		created() {
			console.log(this.$store.state.fans,"粉丝")
			this.request_data();//处理并请求粉丝数据
			//this.fensi = this.$store.state.fans;
		}
	}
</script>

<style scoped="scoped">
	.chat_box {
		width: 100%;
		height: 1.7rem;
		margin-top: 0.02rem;
	}
	
	.imgs {
		width: 1.3rem;
		height: 1.3rem;
		margin: 0.2rem;
		border-radius: 50%;
	}
	
	.text_box {
		width: 5.78rem;
		height: 100%;
		border-bottom: 0.005rem solid #2C3E50;
		float: right;
		position: relative;
	}
	
	.biao {
		font-size: 0.26rem;
		width: 1.3rem;
		height: 0.54rem;
		border: 0.01rem solid black;
		text-align: center;
		line-height: 0.54rem;
		float: right;
		position: absolute;
		right: 0.2rem;
		bottom: 0.5rem;
		border-radius: 0.11rem;
	}
</style>


