<template>
	<div style="width: 100%;">
		<div style="width: 100%;height:13.33rem;position:relative;z-index: 300;background: white;">
		<div class="box_top" style="width: 100%;height: 0.88rem;background: white;position: fixed;top: 0;left: 0;z-index: 99;line-height: 0.88rem;">
	   		<img @click="hui" style="width: 0.4rem;height: 0.4rem;float: left;margin:0.25rem 0.3rem;" src="../../assets/img/zuo.png" alt="" />
	   		<p style="font-size: 0.36rem;margin-left: 3.1rem;">我的作品</p>
	   </div><div style="height: 0.88rem;"></div>
		
		
		
		<div  v-for="(i,index) in actives" @click="to_zuopin(i)" class="boxs">
		<div  @click="to_zuopin(i)">
		<div class="chat_box">
		<span style="line-height:1.2rem;margin-left: 0.3rem;float: left;">
			<p style="float: left;font-size: 0.4rem;font-weight: 600;">{{i.insertDate.charAt(8)}}{{i.insertDate.charAt(9)}}日</p>
			<p style="float: left;font-size: 0.25rem;margin-left: 0.1rem;font-weight: 600;">{{i.insertDate.charAt(5)}}{{i.insertDate.charAt(6)}}月</p>
		</span>
		</div>
		
		<div style="overflow:hidden;vertical-align:middle;text-align:center;" class="img_box" v-if="i.picInfo.length==1" >
			<img style="max-height:auto;width:100%;" :src="i.picInfo[0].picUrl" alt="" />
		</div>
		
		<div class="img-box" v-else-if="i.picInfo.length==2" >
			
		   <div style="height:5rem;width:49.7%;float:left;vertical-align:middle;text-align:center;overflow:hidden;">
			<img style="height:100%;max-width:auto;" :src="i.picInfo[0].picUrl" alt="" />
			</div>
			
			<div style="height:5rem;width:49.7%;float:right;vertical-align:middle;text-align:center;overflow:hidden;">
			  <img style="height:100%;max-width:auto;" :src="i.picInfo[1].picUrl" alt="" />
			</div>
			
		</div>
		
		 <div class="img_box" v-else-if="i.picInfo.length==3" >
	  <!--大图容器-->
	       <div style="width:5rem;height:5rem;float:left;overflow:hidden;vertical-align:middle;text-align:center;">
		 	 <img style="height:100%;max-width:auto;" :src="i.picInfo[0].picUrl"></img>
		   </div> 
		 	 <div style="width: 2.44rem;height: 5rem;float: left;margin-left: 0.04rem;">
	  <!--小图容器上面--> 
	           <div style="width:100%;height:2.47rem;overflow:hidden;vertical-align:middle;text-align:center;">
	           	   <img style="max-height:100%;max-width:auto;" :src="i.picInfo[1].picUrl"></img>
	           </div>
		 	 	
	  <!--小图容器下面-->  
	            
	            <div style="width:100%;height:2.47rem;overflow:hidden;vertical-align:middle;text-align:center;margin-top: 0.04rem;">
	           	   <img style="max-height:100%;max-width:auto;" :src="i.picInfo[2].picUrl"></img>
	           </div>
	            
		 	 </div>
		 </div>
		 </div>	
		 
		 
	  <!--文本容器-->		
	   <div style="width: 100%;box-sizing: border-box;padding: 0.37rem;">
	  <!--文本内容-->
	      <p style="font-size: 0.42rem;margin-bottom: 0.2rem;">{{i.artTitle}}</p>
	   	  <p class="text_span">
	   	  	 {{i.artRemark}}
	   	  </p>
	   </div>
		
		<div class="boxs_to">
			
			
			
			
			<!--<span style="font-size: 0.4rem;float: left;margin-top: 0.04rem;margin-left: 0.6rem;color: #CACACA;">
				<i style="margin-left: 0.04rem;" class="fa fa-comment-o"></i><br />
				<p style="font-size: 0.22rem;">评论</p>
			</span>-->
			
			<span style="font-size: 0.4rem;float: right;margin-top: 0.04rem;margin-right: 0.2rem;">
				<i style="margin-left: -0.05rem;color: #CACACA;" class="fa fa-shopping-basket"></i><br />
				<p style="font-size: 0.22rem;color: #CACACA;">删除</p>
			</span>
			
			<span style="font-size: 0.4rem;float: right;margin-top: 0.04rem;margin-right: 0.4rem;color: #CACACA;">
				<i style="margin-left: 0.04rem;" class="fa fa-share"></i><br />
				<p style="font-size: 0.22rem;">转发</p>
			</span>
			
		</div>
		<div style="width: 100%;height: 0.3rem;background: #F6F6F6;"></div>
		</div>
		
		
		
		
		
		</div>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	import store from '../../vuex/store.js'
	import md5 from 'js-md5';
	export default{
		store,
		data(){
			return{
//				act:[
//				  {h:'尾巴开箱'},
//				  {h:'用户ID'},
//				   {h:'用户ID'},
//				],
				likes:[],
				
				
				yang_si:{noa1:true,noe1:false,noa2:true,noe2:false,noa3:true,noe3:false},
				
				actives:[],
				
			}
		},
		methods:{
			
			hui(){
//				window.location = '#/myspace'
				this.$router.push({
						path: '../myspace'
						});
			},
			
			
			huoqushuju(){//页面刷新获取数据
				var url="http://www.ding-new.com:8080/a10/api/map/art/selectFlag.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
				
				console.log(this.$store.state.data.memId)
				this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/art/selectFlag.do?memId="+id+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				emulateJSON: false,
				method: 'post',
				body: {
					opId:this.$store.state.data.memId,
					typeId:'1'
				}
			}).then(function(response) {
				
				if(response.body.meta.res == '00000'){
                   
                    this.actives = response.body.data.artResponseList
                    
                     console.log( this.actives,'嘻嘻嘻嘻')
				}
			}).catch(function(err){
				console.log(err)
			})
			
			
			
			
		  },
		  to_zuopin(i){
		  	
		  	this.$store.state.worksName = '作品'
		  	this.$store.state.back_id = 3
		  	this.$store.state.worksId = i.artId
		  	
//		  	 window.location = '/#/works_detail'
		  	 this.$router.push({
						path: '../works_detail'
						});
		  	
		  },
		  
		},
		computed:{
			
		},
		mounted(){
			this.huoqushuju()
			
			
		}
	}
</script>

<style scoped="scoped">
	.boxs{
		width: 100%;
		background: white;
		
	}
	.chat_box{
		width: 100%;
		height: 1.2rem;
		margin-top: 0.02rem;
	}
	.imgs{
		width: 1rem;height:1rem;
	   margin: 0.1rem 0.25rem;
	   border-radius: 50%;
	}
	.text_box{
		width: 5.78rem;
		height: 100%;
		
		float: right;
		position: relative;
		line-height: 1.2rem;
	}
	.img_box{
		width: 100%;
		/*height: 5rem;*/
		
		vertical-align:middle;text-align:center;
		overflow: hidden;
	}
	.text_span{
		
		font-size: 0.28rem;
		color: #999999;
	}
	.boxs_to{
		width: 90%;
		height: 1rem;
		margin: 0 auto;
		position: relative;
		border-top: 0.005rem solid #666666;
	}
	
	.imgs{
		display: none;
	}
	.imgs_to{
		display: block;
	}
</style>


