<template>
	<div style="width: 100%;height:13.33rem;position: relative;z-index: 9992;background: white;">
		<!--
        	作者：2443611475@qq.com
        	时间：2018-04-12
        	描述：我的关注
        -->
		<div style="width: 100%;height: 0.88rem;position: fixed;top:0;left: 0;z-index: 9992;line-height: 0.88rem;border-bottom: 0.005rem solid #999999;">
			<img @click="hui" style="width: 0.4rem;height: 0.4rem;float:left;margin:0.25rem 0.3rem;" src="../../assets/img/zuo.png" alt="" />
	   		<p style="font-size: 0.36rem;margin-left: 3.1rem;">我的关注</p>
		</div>
		<div style="height: 0.88rem;"></div>
			
		<div class="chat_box" v-for="item in new_active_fs">
			<img class="imgs" style="" :src="item.opPic" alt="" />
			
			<div class="text_box">
				
				<p style="font-size: 0.38rem;margin-top: 0.45rem;margin-left: 0.2rem;">{{item.nickname}}</p>
				<p style="font-size: 0.24rem;color: #B1B1B1;margin-left: 0.2rem;">{{item.signature}}</p>
				<div class="biao">已关注</div>
			</div>
		</div>
		<div class="chat_box" v-for="item in new_my_friend">
			<img class="imgs" style="" :src="item.opPic" alt="" />
			
			<div class="text_box">
				
				<p style="font-size: 0.38rem;margin-top: 0.45rem;margin-left: 0.2rem;">{{item.nickname}}</p>
				<p style="font-size: 0.24rem;color: #B1B1B1;margin-left: 0.2rem;">{{item.signature}}</p>
				<div class="biao">互为好友</div>
			
			</div>
		</div>
		
	</div>
</template>

<script>
	import md5 from 'js-md5';
	import store from '../../vuex/store'
	export default{
		store,
		data(){
			return{
			   attentions:[],//旧关注
			   my_friend:[],//旧好友
			   new_active_fs:[],//新关注
			   new_my_friend:[],//新好友
			   
			}
		},
		methods:{
			hui(){
//				window.location ='#/myspace'
				 this.$router.push({
						path: '../myspace'
						});
			},
			/*获取关注列表*/
			getAttention(){
				var url = this.$store.state.request_url + "/api/map/user/artUserLists.do";
				var memId =this.$store.state.data.memId;
				var ts = new Date().getTime();
				var token = this.$store.state.data.tokEn;
				var sign = md5(url + memId + token + ts);
				
				this.$http({
				url: this.$store.state.request_url + "/api/map/user/artUserLists.do?memId="+memId+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					"sIgn":sign
				},
				emulateJSON: false,
				method: 'post',
				body: {
					opId:this.$store.state.data.memId,
					typeId:'3'
				}
			}).then(function(response) {
				if(response.body.meta.res == '00000'){
                    this.attentions = response.body.data
                    console.log("___________关注_________________")
                    console.log(this.attentions)
                    /*确保有数据才进行下一步请求*/
                    if(this.attentions.length>0){
                    	this.getFriend();
                    }
                    
				}
			}).catch(function(err){
				console.log(err)
			})
			},
			/*获取好友列表*/
			getFriend(){
				var url = this.$store.state.request_url + "/api/map/user/artUserLists.do";
				var memId =this.$store.state.data.memId;
				var ts = new Date().getTime();
				var token = this.$store.state.data.tokEn;
				var sign = md5(url + memId + token + ts);
				
				this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/user/artUserLists.do?memId="+memId+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					"sIgn":sign
				},
				emulateJSON: false,
				method: 'post',
				body: {
					opId:this.$store.state.data.memId,
					typeId:'1'
				}
			}).then(function(response) {
				
				if(response.body.meta.res == '00000'){
                    this.my_friend = response.body.data
                    console.log("___________好友_________________")
                    console.log(this.my_friend)
                   
                   	this.find_fs();//找关注
                    this.find_friend();//找好友
				}
			}).catch(function(err){
				console.log(err)
			})
			},	
			/*判断是否在数组内*/
			find_tong(arr,str){
				
				for(var i = 0; i < arr.length; i++){
        			if(str === arr[i].opId){
            		return true;
        		}
    		}
    			return false;
			},
			/*找关注*/
			find_fs(){
				for(var i=0;i<this.attentions.length;i++){
						
						if(!this.find_tong(this.my_friend,this.attentions[i].opId)){
								/*防止重复push*/
							if(!this.find_tong(this.new_active_fs,this.attentions[i].opId)){
								this.new_active_fs.push(this.attentions[i])
								
							}	
						}
				}
				
			},
			/*找好友*/
			find_friend(){
				for(var i=0;i<this.attentions.length;i++){
						
						if(this.find_tong(this.my_friend,this.attentions[i].opId)){
								/*防止重复push*/
							if(!this.find_tong(this.new_my_friend,this.attentions[i].opId)){
								this.new_my_friend.push(this.attentions[i])
								
							}	
						}
				}
				
			}
			
		},
		
		mounted(){
			this.getAttention();	
		},
		computed:{
			
		}
	}
</script>

<style scoped="scoped">
	.chat_box{
		width: 100%;
		height: 1.7rem;
		margin-top: 0.02rem;
	}
	.imgs{
		width: 1.3rem;height:1.3rem;
	   margin: 0.2rem;
	   border-radius: 50%;
	}
	.text_box{
		width: 5.78rem;
		height: 100%;
		border-bottom:0.005rem solid #2C3E50 ;
		float: right;
		position: relative;
	}
	.biao{
		font-size: 0.26rem;
		width: 1.3rem;
		height:0.54rem;
		border: 0.01rem solid #CACACA;
		text-align: center;
		line-height: 0.54rem;
		float: right;
		position: absolute;
		right: 0.2rem;
		bottom: 0.5rem;
		border-radius: 0.11rem;
		color: #CACACA;
	}
</style>


