<template>
	<div id="app">
		<div class="head">
			<img src="../assets/img/back_blank.png" @touchstart="back"/>
		</div>
		<div class="big_img">
			<img :src="photoUrl" />
		</div>
		<div class="footer">
			<div class="function">
				<div class="function_item_left" @touchstart="goodTap">
					<img :src="call?'../../static/img/icon_detail_good_active.png':'../../static/img/icon_photo_good.png'" />
					<p>点赞</p>
				</div>
				<div class="function_item_left" @touchstart="collectTap">
					<img :src="collect?'../../static/img/icon_detail_collect_active.png':'../../static/img/icon_photo_collect.png'" />
					<p>收藏</p>
				</div>
				<div class="function_item_left">
					<img src="../assets/img/icon_photo_share.png" />
					<p>转发</p>
				</div>
				<div class="function_item_left">
					<img src="../assets/img/icon_photo_comment.png" />
					<p>评论</p>
				</div>
				<div class="function_item_right">
					<img src="../assets/img/icon_photo_tip.png" />
					<p>打赏</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import store from '../vuex/store.js'
	import { Toast } from 'mint-ui';
	import md5 from 'js-md5';
	
		export default {
//			el:"#app",
			store,
			data(){
				return{
					call:false,
					collect:false,
					photoUrl:''
				}
				
			},
			methods: {
				back(){
					this.$router.push({
							path: '../personal_data'
						})
					//	window.location = '/#/personal_data'
				},
				goodTap(){
					
					var name="";
					if(this.$store.state.photoUrl.artFlag=="1"){
						name="作品";
					}else if(this.$store.state.photoUrl.artFlag=="2"){
						name="闲情";
					}else if(this.$store.state.photoUrl.artFlag=="3"){
						name="文章";
					}
					
					console.log(this.$store.state.photoUrl);
					   var url="http://www.ding-new.com:8080/a10/api/map/art/artLike.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
					    
					
						this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/art/artLike.do?memId="+id+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								'sIgn': sign
							},
							body: {
								artId: this.$store.state.personal_c.artId,//作品Id
								likeType:name,//类型
								opId:this.$store.state.data.memId,//当前用户Id
								likeOpId:this.$store.state.personal_c.opId//被点赞用户Id
							},
							emulateJSON: false,
						}).then(function(response) {
							console.log("点赞");
							console.log(response);
							if(response.body.meta.res == "00000"||response.body.meta.res == "99999") {
								this.call=true
								/*提示语*/
								Toast({
									message: '点赞成功',
									position: 'middle',
									duration: 2000
								});
								//this.worksComment = response.body.data;
							}
						}).catch(function(err) {
							console.log(err)
						})
					
					
					
				},
				collectTap(){
					this.collect=!this.collect;
					/*接口没出*/
//					if(this.collect==false){
//						this.$http({
//							url: 'http://www.ding-new.com:8080/a10/api/map/art/artCollect.do',
//							method: 'post',
//							headers: {
//								"content-type": "application/json;charset=UTF-8"
//							},
//							body: {
//								artId: this.data[index].artId,//作品ID
//								collectType:"作品",//类型
//								opId:this.$store.state.data.memId,//当前用户Id
//								collectOpId:this.data[index].opId//被收藏的Id
//							},
//							emulateJSON: false,
//						}).then(function(response) {
//							
//							if(response.body.meta.res == "00000") {
//								console.log("收藏");
//								this.collect=true;
//								/*提示语*/
//								Toast({
//									message: '收藏成功',
//									position: 'middle',
//									duration: 2000
//								});
//							}
//						}).catch(function(err) {
//							console.log(err)
//						})
//					}else{
//						this.collect=false;
//						/*提示语*/
//								Toast({
//									message: '取消收藏成功',
//									position: 'middle',
//									duration: 2000
//								});
//					}
					
					
				}
			},
			mounted() {
				this.photoUrl= this.$store.state.photoUrl.picUrl.picInfo[0].picUrl
			}
		
			

	}
</script>

<style scoped="scoped">
	#app{
		overflow-x: hidden;
		background-color: #000000;
		height: 13.32rem;
		
	}
	li {
		list-style-type: none;
	}
	*{
		font-family: "微软雅黑";
	}
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	.head{
		margin-top: 0.5rem;
		margin-left: 0.25rem;
		width: 100%;
		height: 0.5rem;
	}
	.head img{
		width: 0.5rem;
		height: 0.5rem;
	}
	.big_img{
		margin-top: 0.5rem;
		height: 9rem;
		width: 100%;
	}
	.big_img img{
		width: 100%;
		height: 100%;
	}
	.footer{
		width: 100%;
		height: 1.75rem;
		position: fixed;
		bottom: 0;
		
	}
	
	.function {
		margin-top: 0.25rem;
		width: 100%;
		height: 0.75rem;
	}
	
	.function_item_left {
		float: left;
		width: 20%;
		text-align: center;
		height: 100%;
	}
	
	.function_item_left img,
	.function_item_right img {
		width: 35%;
		height: 0.4rem;
	}
	
	.function_item_left p,
	.function_item_right p {
		font-size: 0.3rem;
		color: #fff;
		line-height: 0.75rem;
	}
	
	.function_item_right {
		float: right;
		width: 20%;
		text-align: center;
		height: 100%;
	}
</style>


