<template>
	<div class="mid-center" style="position: relative;">
       
       
		<dashang @refreshList="dashangs" v-show='$store.state.dashang'></dashang>
        
        <div style="width: 100%;position: fixed;top: 0;">
		<img style="position: absolute;width: 2rem;top: 0.4rem;left: 0.33rem;" src="../../assets/img/drawable-xxxhdpi/icon_home_title.png" />
		<img @touchstart="to_Conditionscreening" style="position: absolute;top: 0.46rem;right: 0.33rem;width: 0.4rem;" src="../../assets/img/drawable-xxxhdpi/icon_search.png" alt="" />

		<div class="stack-wrapper">
			<!--
     	作者：2443611475@qq.com
     	时间：2018-03-30
     	描述：:pages="someList"
     -->
			<stack ref="stack" :stackinit="stackinit"></stack>
		</div>
       </div>
               <div v-show="dal" style="width:100%;height: 13.3rem;background: rgba(0,0,0,0.5);position: fixed;top: 0;">
               	     <div class="da" style="width:4rem;height:25%;background:white;margin: 5rem 1.9rem;overflow:hidden;position: relative;">
               	     	<p class="p_text">当前没有账号登录，是否前往登录？</p>
               	     	 
               	     	 <div style="width:100%;height:25%;position: absolute;bottom: 0;left: 0;">
               	     	 	
               	     	 	<button style="background: white;" class="btn">取消</button>
               	     	 	
               	     	 	<button @click="queren" style="background: black;color: white;" class="btn">确认</button>
               	     	 	
               	     	 </div>
               	     </div>
               </div>
	</div>
</template>
<script>
	import stack from './stack.vue'
	import dashang from './dashang.vue'
	import store from '../../vuex/store.js'
	export default {
		//el: '#stack',
		store,
		data() {
			return {
				someList: [],
				stackinit:{
					visible: 3
				},
				das: {
					da: false
				},
				dal:false,

			}
		},
		
		components: {
			stack,
			dashang,
		},
		methods: {
			
			queren(){
				this.$router.push({
						path: '../home'
					});
						
//				window.location='/home'		
				
			  },
			//  prev () {
			//    this.$refs.stack.$emit('prev')
			//    
			//  },
			//  next () {
			//    this.$refs.stack.$emit('next')
			//  },
			//  tops () {
			//    this.$refs.stack.$emit('tops')
			//  }
			
			   dashangs(){
			   	  this.$refs.stack.$emit('test')
			   	  
			   },
			  
      huoqu(){
//    	this.$http({
//				url: "http://www.ding-new.com:8080/a10/api/map/art/getDisplayArtList.do",
//				headers: {
//					"content-type": "application/json;charset=UTF-8"
//				},
//				emulateJSON: false,
//				method: 'post',
//				body: {
//					opId: '12',
//					page: '1',
//					rows: '50'
//				}
//			}).then(function(response) {
//				console.log(response)
//				if(response.body.meta.res == '00000') {
////					console.log(response.body)
//					//						let that = this
//					//  setTimeout(function () {
//					this.someList = response.body.data
//
//					//  }, 1000)
//				}
//			}).catch(function(err) {
//				console.log(err)
//			})
    },
			to_Conditionscreening(){
					this.$router.push({
						path: '../Conditionscreening'
					});
			},
			/*连接*/
			connectWebSocket(id){
				// 打开一个 web socket
                
				this.ws = new WebSocket("ws://120.76.188.131:7777?opId="+id);
				
			
				/*判断是否处于连接状态*/
				if(this.ws.readyState != 1) {
				    console.log(this.ws)
				    
				var that = this
				/*连接*/
				this.ws.onopen = function() {
					console.log("连接成功",that.ws)
					that.$store.state.new_ws=that.ws;
					that.ws.onmessage = that.getMessage; /*接受推送信息*/
					}
				}
				/*异常触发*/
				this.ws.onerror = function(){
					alert("异常...");
				}
				
			},
			/*接收信息*/
			getMessage(evt) {
				var received_msg = evt.data;
				console.log(evt.data,"推送信息")
				if(received_msg != "链接成功,您可以开始聊天" && received_msg != "您的好友已下线！") {

					//将json字符串转为json对象
					var json = eval('(' + received_msg + ')');
					console.log(json);
					
					switch(json.msgType){
						case 'msg'://聊天信息
							
							this.$store.state.is_infro=true;
							this.$store.state.msgs.push(json)  
							console.log(this.$store.state.msgs_to.length,"长度")
						if(this.$store.state.msgs_to.length>0){
							console.log("-----------1---------")
							let a = 0
							for(var i=0;i<this.$store.state.msgs_to.length;i++){
								console.log("-----------"+i+"---------")
								console.log(this.$store.state.msgs_to[i].fromOpId == json.fromOpId,"是否相等")
								if(this.$store.state.msgs_to[i].fromOpId == json.fromOpId){
									this.$store.state.msgs_to[i] = json
									a = 0
										break;
								}else{
									a = 1
								}
								
							}
							
							if(a==1){
								console.log("进来")
								this.$store.state.msgs_to.push(json)
							}
							
						}else{
							this.$store.state.msgs_to.push(json)  
						}
//							 
//							 this.$router.push({
//						path: '../message/chat'
//						});
							 
							console.log(this.$store.state.msgs_to,"聊天")
							break;
							
						case 'fans'://粉丝通知
							this.$store.state.fans.push(json.message)
							console.log(this.$store.state.fans,"粉丝")
							break;
							
						case 'friend'://好友通知
							if(this.$store.state.is_login){
								this.$store.state.friends.push(json)
							}else{
								this.$store.state.friends_to=json;
							}
							break;
							
						case 'system'://系统通知
							this.$store.state.systems.push(json.message)
							break;
							
						case 'like'://点赞通知
							this.$store.state.likes.push(json.message)
							console.log(this.$store.state.likes,"点赞")
							break;
							
						case 'discuss'://评论通知
							this.$store.state.discuss.push(json.message)
							console.log(this.$store.state.discuss,"评论")
							break;
							
						case 'reward'://打赏通知
							this.$store.state.rewards.push(json.message)
							console.log(this.$store.state.rewards,"打赏")
							break;
							
						case 'complaintSuggestion'://建议通知
							this.$store.state.complaintSuggestions.push(json.message)
							break;
					}

				} else {

					console.log(received_msg)
				}
		},
			
			dals(){
				 
			}
		},
		mounted(){
			
			this.$http({
				
					url: this.HOST,
					
					method: 'POST',
					
					headers: {
						
						"content-type": "application/json"
						
					},
					
					emulateJSON: true,
					
				}).then(function(response){
					
					        this.$store.state.dy = response.body.content.address
					        
							console.log( this.$store.state.dy,'地理位置信息1111111111111111111111111111111111')
							
						}).catch(function(err){
							
							console.log(err)
							
						})
			
			 this.huoqu()
              
//            window.location.href='#/slider'
             
             this.dals()
		},
		
		created(){
			
			this.$store.state.data.memId = localStorage.memId;
               this.$store.state.data.tokEn = localStorage.tokEn;
                this.$store.state.data.info = localStorage.info;
               console.log(this.$store.state.data.memId,"121212")
//            
//            if(localStorage.yidao != 1){
//            	  console.log(111111111)
//            	  this.$router.push({
//						path: '../home'
//						});
//						
//            }
              
              if(!this.$store.state.data.memId){
              	
//                this.$router.push({
//						path: '/home'
//					});   			  
                    
                    this.dal = true
              }else{
              		if(this.$store.state.new_ws){
						if(this.$store.state.new_ws.readyState!=1){
							var that = this
							/*连接*/
							this.$store.state.new_ws.onopen = function(){
								console.log("连接成功")
								that.$store.state.new_ws.onmessage = that.getMessage; /*接受推送信息*/
							}
						}
					}else{
						this.connectWebSocket(this.$store.state.data.memId)
					}
              }
              
              
              
		}
		
	}
</script>
<style scoped="scoped">
	.stack-wrapper {
		
		/*list-style: none;
		pointer-events: none;*/
		
		/*position: fixed;top:0;z-index: -1;*/
		
		
	}
	.da{
		border-radius: 0.2rem;
		padding-top:0.5rem ;
	}
	.p_text{
		font-size: 0.4rem;
		width: 90%;
		
		margin-left: 9%;
		
	}
	.btn{
		width: 50%;
		height: 100%;
	    font-size: 0.3rem;
		float: left;
		border: none;
	}
</style>


