<template>
	<div v-show="m.dad" id="app">
		<div @click="hide" style="width: 100%;height: 6rem;position:fixed;top: 0;left: 0;background:rgba(0,0,0,0.4);z-index:4;">

		</div>
		<div class="return_text">
			<div class="athor_infor">
				<div class="athor_infor_left">
					<div class="athor_icon">
						<img :src="$store.state.worksCommentContent.disUserPic" />
					</div>
					<p class="athor_name">{{$store.state.worksCommentContent.disUser}}</p>
					
				</div>
				<div class="athor_infor_right">
					<div class="call">
						<p>{{$store.state.worksCommentContent.disLike}}</p>
						<img src="../../assets/img/icon_comment_good.png" />
					</div>
					<div class="call_right">
						<img src="../../assets/img/icon_comment_translation.png" />
					</div>
				</div>
			</div>
			
			
			
			<div style="" class="athor_infor_content">
				<p style="width: 100%;margin-bottom: 0.1rem;" class="athor_infor_content_text">{{$store.state.worksCommentContent.disContent}}</p>
				<p class="date">{{$store.state.worksCommentContent.disDate}}</p>
			</div>
			<div class="hui_content">
				<dt></dt>
				<p class="all">全部回复</p>
				<dt></dt>
			</div>
			
			<div class="content">
				<div v-for="arr in $store.state.pinlun_child" @click="showD(arr)">
					<div class="athor_infor">
						<div class="athor_infor_left">
							<div class="athor_icon">
								<img :src="arr.disUserPic" />
							</div>
							<p class="athor_name">{{arr.disUser}}</p>
							<!--<div class="athor_div" v-if="arr.athor=='作者'?true:false">
								<p class="athor_text">作者</p>
							</div>
							<div class="lou" v-if="arr.athor=='楼主'?true:false">
								<p class="athor_text">楼主</p>
							</div>-->
						</div>
						<div class="athor_infor_right">
							<div class="call">
								
								<img src="../../assets/img/icon_comment_good.png" />
							</div>
							<div class="call_right" >
								<img src="../../assets/img/icon_comment_translation.png" />
							</div>
						</div>
					</div>
                      
                      <div class="p_texts"><p>{{arr.disContent}}</p></div>
                      
					<div class="athor_infor_content">
						
						<p class="date">{{arr.disDate}}</p>
					</div>
					<div class="down_xian"></div>
				</div>
				<p class="no_more">已无更多评论</p>
			</div>

			<div class="footer">
				<textarea ref="mo_va" type="text" :placeholder='"回复@"+$store.state.worksCommentContent.disUser+"："'></textarea>
				 <div @click.stop="n1" class="send">
					<p>发送</p>
				 </div>
			</div>
		</div>
		<showdialog v-bind:d='mes'></showdialog>
	</div>

</template>

<script>
	import showdialog from './show_dialog.vue'
	import md5 from 'js-md5';
	import { Toast } from 'mint-ui';
	export default {

		components: {
			showdialog
		},
		data() {
			return {
				dad: {
					noa: true,
					noe: false
				},
				mes: {
					is_show: false
				},
				acct: '',
				da: '',
			
			}
		},
		props: ['m'],
		methods: {
			
             n1(){//一级回复评论
             
             	var disUser ='';
             	var disUserPic ='';
                 var identification ='';
                 var mo_va=this.$refs.mo_va.value;
                 
             	if(mo_va!=''){
             		var url="http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign=md5(url+id+token+ts)
             		this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do?memId="+id+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								'sIgn': sign
							},
							body: {
								opId:this.$store.state.data.memId,
								
							},
							emulateJSON: false,
						}).then(function(response) {
							 
							if(response.body.meta.res == "00000") {
								console.log(response.body)
								disUser = response.body.data.nickname
								disUserPic = response.body.data.opPic
								identification = response.body.data.idCard
								
								console.log(disUser,'1111111',disUserPic)
                               
                               if(disUser!==''&&disUserPic!==''){
                 	              console.log("评论");
                 	        var url1="http://www.ding-new.com:8080/a10/api/map/art/artDis.do";      
                 	          var sign1=md5(url1+id+token+ts)    
                 	              
            	  this.$http({
							url: 'http://www.ding-new.com:8080/a10/api/map/art/artDis.do',
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								'sIgn': sign1
							},
							body: {
								disId:this.$store.state.worksCommentContent.disId,
								opId:this.$store.state.data.memId,
								disOpId:this.$store.state.works_ads.opId,
								artId:this.$store.state.worksId,
								disContent:mo_va,
								disUser:disUser,
								disUserPic:disUserPic,
								identification:"vip"
							},
							emulateJSON: false,
						}).then(function(response) {
							  console.log(response.body,"二级回复状态");
							if(response.body.meta.res == "00000") {
								//this.worksComment = response.body.data;
								
								Toast({
											message: '回复成功',
											position: 'middle',
											duration: 2000
										});
								this.plhf()	
								this.$refs.mo_va.value = ''
								
								this.$emit('huoqu');
								
								
							}
						}).catch(function(err) {
							console.log(err)
						})
						
						}
                 
							}
						}).catch(function(err) {
							console.log(err)
						})
             		
                 
						}else{
							Toast({
											message: '评论内容不能为空',
											position: 'middle',
											duration: 2000
										});
							
						}
						
						
            },
			showss() {

			},
			hide() {
				this.m.dad = false
			},
			showD(arr) {
				this.mes.is_show = true
				this.$store.state.input_x = arr
				
				
			},
			
			
			plhf(){//获取回复状态
				var url="http://www.ding-new.com:8080/a10/api/map/art/selectArtDis.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign=md5(url+id+token+ts)
				this.$http({
							url: "http://www.ding-new.com:8080/a10/api/map/art/selectArtDis.do?memId="+id+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								'sIgn': sign
							},
							body: {
								artId:this.$store.state.worksId,
								parentId:this.$store.state.worksCommentContent.disId,
								
							},
							emulateJSON: false,
						}).then(function(response) {
							console.log(response.body,"一级回复状态");
							if(response.body.meta.res=="00000"){
								//this.worksComment = response.body.data;
								
								this.$store.state.pinlun_child = response.body.data.artDis;
								
							}
						}).catch(function(err) {
							console.log(err)
						})
			},
			
		},
		mounted() {
//			this.da = this.$store.state.pinlun_child
			//this.da = this.$store.state.act;

		}

	}
</script>

<style scoped="scoped">
	.p_texts{
		font-size: 0.28rem;
		
		padding-left: 0.75rem;
		color: #495060;
		width: 7.3rem;
		word-break:break-all;/* 不换行 */ 
        white-space:normal;/* 不换行 */ 
	}
	input,textarea{
		outline: none;
	}

	li {
		list-style-type: none;
	}
	
	* {
		font-family: "微软雅黑";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	
	.athor_infor {
		margin-top: 0.5rem;
		width: 100%;
		height: 0.6rem;
	}
	
	.athor_icon {
		width: ;
		margin-left: 0.15rem;
		height: 0.6rem;
	}
	
	.athor_icon img {
		width: 0.6rem;
		height: 100%;
		border-radius: 50%;
	}
	
	.athor_name {
		margin-top: 0.25rem;
		float: left;
		font-size: 0.25rem;
		margin-left: 0.1rem;
		color: rgba(177, 177, 177, 1);
		line-height: 0.165rem;
	}
	
	.athor_div {
		margin-top: 0.2rem;
		float: left;
		margin-left: 0.1rem;
		width: 20%;
		height: 0.25rem;
		text-align: center;
		background-color: #000000;
		border-radius: 0.05rem;
	}
	
	.lou{
		margin-top: 0.2rem;
		float: left;
		margin-left: 0.1rem;
		width: 20%;
		height: 0.25rem;
		text-align: center;
		background-color: #67B9F8;
		border-radius: 0.05rem;
	}
	.athor_text {
		padding: 0.05rem;
		font-size: 0.185rem;
		color: rgba(255, 255, 255, 1);
		line-height: 0.125rem;
	}
	
	.athor_infor_left {
		width: 40%;
		height: 100%;
		float: left;
	}
	
	.athor_icon {
		float: left;
	}
	
	.athor_infor_right {
		width: 30%;
		height: 100%;
		float: right;
	}
	
	.athor_infor_content {
		padding-top: 0.25rem;
		margin: 0 0.25rem 0 0.75rem;
	}
	
	.athor_infor_content_text {
		width: 5rem;
		/*background: red;*/
		font-size: 0.305rem;
		color: rgba(51, 51, 51, 1);
		line-height: 0.335rem;
	}
	
	.call {
		margin: 8%;
		width: 50%;
		float: left;
	}
	
	.call p {
		font-size: 0.3rem;
		color: #D0D0D0;
		float: left;
		line-height: 0.5rem;
	}
	
	.call img {
		float: right;
		width: 0.4rem;
		height: 0.4rem;
	}
	
	.call_right {
		float: right;
		height: 100%;
		margin-right: 0.25rem;
	}
	
	.call_right img {
		width: 0.5rem;
		height: 0.4rem;
		margin: 30% 0;
	}
	
	.date {
		padding-top: 0.1rem;
		font-size: 0.26rem;
		color: rgba(198, 198, 198, 1);
		line-height: 0.335rem;
	}
	
	.all {
		width: 100%;
		
		text-align: center;
		color: rgba(51, 51, 51, 1);
		font-size: 0.3rem;
		
	}
	
	.return_text {
		height: 90%;
		position: fixed;
		width: 100%;
		bottom: 0;
		z-index: 4;
		background-color: #fff;
		border-top-left-radius: 0.45rem;
		border-top-right-radius: 0.45rem;
	}
	
	.content {
		height: 8.1rem;
		overflow-y: scroll;
		overflow-x: hidden;
		padding-bottom: 1.5rem;
	}
	
	.footer {
		height: 1rem;
		width: 100%;
		position: fixed;
		bottom: 0;
		z-index: 4;
		-moz-box-shadow: 0 0 0.3rem rgba(247,247,247,1); /* 老的 Firefox */
		box-shadow: 0 0 0.3rem rgba(247,247,247,1);
		background: white;
	}
	
	.footer textarea {
		outline: none;
		float: left;
		border: 0;
		width: 70%;
		font-size: 0.35rem;
		margin-top: 0.1rem;
		height: 0.8rem;
		margin-left: 3%;
		color: rgba(180, 180, 180, 1);
		border: #D8D8D8 solid 1px;
		line-height: 0.8rem;
		padding-left: 0.2rem;
		opacity:0.01rem;
		border-radius: 0.15rem ; 
	}
	
	
	.no_more{
		text-align: center;
		font-size: 0.3rem;
		line-height: 1rem;
		color: #D0D0D0;
	}
	.send {
		width: 18%;
		background-color: #000;
		height: 0.6rem;
		margin-top: 0.2rem;
		margin-right: 0.25rem;
		border-radius: 0.1rem;
		float: right;
	}
	
	.send p {
		color: #fff;
		padding: 0.1rem;
		text-align: center;
		font-size: 0.3rem;
	}
	
	.box {
		display: none;
	}
	
	.boxto {
		display: block;
	}
	.hui_content{
		width: 90%;
		margin: auto 5%;
		overflow: hidden;
	}
	.hui_content dt{
		width: 30%;
		height: 1px;
		margin:0.25rem 0.3rem 0;
		
		background-color: #D0D0D0;
	}
	.hui_content p{
		width: 20%;
		text-align: center;
	}
	.hui_content dt,.hui_content p{
	float: left;	
	}
	.down_xian{
		width: 90%;
		height: 0.01rem;
		background:rgba(247,247,247,1);
		margin: 0.2rem 0;
		margin-left: 0.75rem;
	}
</style>


