<template>
	<div style="width: 100%;height: 13rem;z-index:300;background:white;position:relative;top: 0.2rem;">
		<div style="width: 90%;position:relative;margin: 0 auto;margin-bottom: 1.5rem;">
			<span @touchstart="hui" style="font-size: 0.4rem;">
			<img style="width: 0.3rem;margin-right: 0.1rem;" src="../../assets/img/zuo.png" alt="" />返回</span>
			
			<!--发布作品 -->

			<!--标题 -->
			<h5 style="font-size: 0.6rem;">发布作品</h5>

			<p style="font-size: 0.3rem;color: #D0D0D0;margin-top: 0.1rem;">
				上传作品并填写资料</p>

			<!--作者：2443611475@qq.com
            	时间：2018-03-27
            	描述：生活照-->
			<div style="float:left; margin-right: 0.2rem;margin-top: 0.2rem;">
				<img class="zhu" :src="$store.state.img_box.picUrl" />
				<input style="opacity:0;margin-left: -2.5rem;" class="zhu" ref="ss" name="file" type="file" accept="image/png,image/gif,image/jpeg" @change="uploadpic" />
			</div>

			<!--
            	作者：2443611475@qq.com
            	时间：2018-03-27
            	描述：补充图
            -->

			<!--<img v-for="(i,index) in $store.state.img_box_to" style="margin-right: 0.2rem;margin-top: 0.2rem;" class="zhu" :src="i.picUrl" />
			<div style="float:left;margin-top: 0.2rem;">
				<img class="zhu" src="../../assets/img/drawablefffff/icon_announce_secondImg.png" />
				<input style="opacity:0;margin-left: -2.5rem;" class="zhu" ref="ss" name="file" type="file" accept="image/png,image/gif,image/jpeg" @change="uploadpic_to" />
			</div>-->

			<input v-model="$store.state.Data_set.The_name_of_the_work" style="font-size: 0.36rem;height:1rem;border: none;width: 100%;border-bottom: 0.005rem solid #666666;" type="text" placeholder="请输入作品名称（必填）" />

			<div @click="to_Typeofwork" class="boxs">
				<span><p style="font-size: 0.3rem;float: left;">作品类型</p>
		     	  <p style="font-size: 0.25rem;float: left;color: #CACACA;">(必填)</p></span>
				<i style="float: right;font-size: 0.45rem;line-height: 1rem;margin-right: 0rem;color: #CACACA;" class="fa fa-chevron-right"></i>
				<p style="font-size: 0.3rem;float: right;margin-right: 0.2rem;">{{biao}}</p>
			</div>
			<!--
             	作者：2443611475@qq.com
             	时间：2018-03-23
             	描述：最终板数、当用户选择指定的作品类型时方才显示
             -->
			<div :class="{type:acts.noa,types:acts.noe}" style="width: 80%;height: 1rem;border-bottom: 0.01rem solid #D2D2D2;line-height: 1rem;margin-left: 1.4rem;">
				<p style="font-size: 0.3rem;float: left;">最终板数</p>
				<p style="font-size: 0.3rem;float: right;">版</p>
				<input v-model="$store.state.Data_set.Version_number" style="float: right;font-size: 0.3rem;width: 1rem;border: none;" type="text" placeholder="请输入" />
			</div>

			<div style="margin-top: 0;" class="boxs">
				<span><p style="font-size: 0.3rem;float: left;">作品版权</p>
		     	  <p style="font-size: 0.25rem;float: left;color: #CACACA;">(必填)</p></span>
				<button @click="btn1" :class="{btnss:act.noa,btnssto:act.noe}">临摹
				   <img :class="{img_lang:act.noa,img_xiao:act.noe}" src="../../assets/img/xia1.png" alt="" />
				</button>
				<button @click="btn2" :class="{btnss:act.noa1,btnssto:act.noe1}">原创
				   <img :class="{img_lang:act.noa1,img_xiao:act.noe1}" src="../../assets/img/xia1.png" alt="" />
				</button>
			</div>

			<div :class="{type:act.noa,types:act.noe}" style="width: 80%;height: 1rem;border-bottom: 0.01rem solid #D2D2D2;line-height: 1rem;margin-left: 1.4rem;">
				<p style="font-size: 0.3rem;float: left;">原作者</p>

				<input v-model="$store.state.Data_set.original_author" style="float:right;font-size: 0.3rem;width: 2rem;border:none;height:0.7rem;margin-top: 0.15rem;" type="text" placeholder="请输入" />
			</div>

			<div @click='qi' style="margin-top: 0;" class="boxs">
				<span><p style="font-size: 0.3rem;float: left;">细节补充</p>
		     	  <p style="font-size: 0.25rem;float: left;color: #CACACA;">(必填)</p></span>
				<i style="float: right;font-size: 0.45rem;line-height: 1rem;margin-right: 0rem;color: #CACACA;" class="fa fa-chevron-right"></i>
			</div>

			<textarea v-model="$store.state.Data_set.Description_of_creation" style="width: 100%;height:1.69rem;font-size: 0.3rem;margin-top: 0.1rem;border: none;" placeholder="填写关于作品的创作说明(限制内容长度512个字)" maxlength="512"></textarea>
		</div>

		<div style="width:100%;height:1.3rem;box-shadow:0px 0px  15px 0.1px #CACACA;position: fixed;bottom: 0;z-index: 999;background: white;">
			<button @click="go_babi" style="width:6.4rem;height:0.9rem; background:rgba(46,49,53,1);border-radius: 0.15rem;color: white;position:absolute;left:0.5rem;top:0.2rem;font-size:0.3rem;">
			      确认发布
			</button>

		</div>
	</div>
</template>

<script>
	import store from '../../vuex/store.js'
	import { Toast } from 'mint-ui';
	import md5 from 'js-md5';
	export default {
		store,
		data() {
			return {
				act: {
					noa: true,
					noe: false,
					noa1: true,
					noe1: false
				},
				acts: {
					noa: true,
					noe: false
				},
				biao: '',

			}
		},
		methods: {
			open3() {
				this.$notify({
					title: '成功',
					message: '作品发布成功',
					type: 'success',
		//        duration:0,
				});
			},

			hui() {
				//				this.$store.state.home_tai = 3
				
					this.$router.push({
						path: '../add_facth'
						});
				this.$store.state.img_box = {};
				this.$store.state.img_box_to = [];

			},
			qi() {
				this.$router.push({
						path: '../Explain'
						});
				
			},
			btn1() {
				this.act.noa = false, this.act.noe = true
				this.act.noa1 = true, this.act.noe1 = false

				this.$store.state.Data_set.Copyright_of_works = '临摹'
			},
			btn2() {
				this.act.noa = true, this.act.noe = false
				this.act.noa1 = false, this.act.noe1 = true

				this.$store.state.Data_set.Copyright_of_works = '原创'
			},

			to_Typeofwork() {
				this.$router.push({
						path: '../Typeofwork'
						});
				
			},
			//上传图片
			uploadpic(e) { //上传主图事件
				var url="http://www.ding-new.com:8080/a10/api/mapManager/uploadPic.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign=md5(url+id+token+ts)
		        
				//                var self = this
				let file = e.target.files[0]
				/* eslint-disable no-undef */
				let param = new FormData() // 创建form对象
				param.append('file', file) // 通过append向form对象添加数据
				param.append('plateName', 'art') // 添加form表单中其他数据
				param.append('picType', 'cover_pic')
				console.log(param.get('file')) // FormData私有类对象，访问不到，可以通过get判断值是否传进去
//				let config = {
//					headers: {
//						'Content-Type':'multipart/form-data',
//						"sIgn":sign
//					}
//				}
				if(file) {
					// 添加请求头
//					this.$axios.post("api/mapManager/uploadPic.do?memId="+id+"&ts="+ts, param, config)
//						.then(response => {
//							if(response.data.meta.res === '00000') {
//								this.$store.state.img_box = response.data.data
//								console.log(this.$store.state.img_box)
//							}
//						})
                     this.$http({
					
                     url:"http://www.ding-new.com:8080/a10/api/mapManager/uploadPic.do?memId="+id+"&ts="+ts,
					method: 'post',
					headers: {
						'Content-Type': 'multipart/form-data',
						'sIgn': sign
					},
					body: param,
					emulateJSON: false,
				}).then(function(response) {
					if(response.body.meta.res == "00000") {
						
                       this.$store.state.img_box = response.data.data
						
						console.log(this.$store.state.img_box)
					}

				})

                       
				}
			},
			uploadpic_to(e){ //上传副图事件
//				if(this.$store.state.img_box_to.length < 8) {
//					var url="http://www.ding-new.com:8080/a10/api/mapManager/uploadPic.do";
//		        var id=this.$store.state.data.memId;
//		        var token=this.$store.state.data.tokEn;
//		        var ts=new Date().getTime();
//		        var sign=md5(url+id+token+ts)
//					
//					var self = this
//					let file = e.target.files[0]
//					/* eslint-disable no-undef */
//					let param = new FormData() // 创建form对象
//					param.append('file', file) // 通过append向form对象添加数据
//					param.append('plateName', 'art') // 添加form表单中其他数据
//					param.append('picType', 'supplementary_pic')
//					console.log(param.get('file')) // FormData私有类对象，访问不到，可以通过get判断值是否传进去
//					let config = {
//						headers: {
//							'Content-Type': 'multipart/form-data',
//							"sIgn":sign
//						}
//					}
//					if(file) {
//						// 添加请求头
//						this.$axios.post("api/mapManager/uploadPic.do?memId="+id+"&ts="+ts, param, config)
//							.then(response => {
//								if(response.data.meta.res === '00000') {
//									this.$store.state.img_box_to.push(response.data.data)
//									console.log(this.$store.state.img_box_to)
//								}
//							})
//					}
//				}
			},
			go_babi() { //作品发布
				this.$store.state.img_box_to = []

				console.log(this.$store.state.Data_set)
				var url="http://www.ding-new.com:8080/a10/api/mapManager/saveArt.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign=md5(url+id+token+ts)
				
				console.log(this.$store.state.img_box.picUrl)
				
                if(this.$store.state.img_box.picId){
				this.$store.state.img_box_to.push(this.$store.state.img_box)
				this.$http({
					
					url: "http://www.ding-new.com:8080/a10/api/mapManager/saveArt.do?memId="+id+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						"sIgn":sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId: this.$store.state.data.memId, //用户登录成功后的信息ID-到时候动态获取
						picUrl: this.$store.state.img_box_to,
						artTitle: this.$store.state.Data_set.The_name_of_the_work,
						artType: {
							artClassification: this.$store.state.Data_set.type_fenlei,
							classificationName: this.$store.state.Data_set.Type_work,
							printPic: this.$store.state.Data_set.Version_number
						},
						artCopyright: this.$store.state.Data_set.Copyright_of_works,
						artDecription: {
							createDecade: this.$store.state.Data_set.nian_dai,
							material: this.$store.state.Data_set.cai_zhi,
							createTheme: this.$store.state.Data_set.ti_cai,
							artLength: this.$store.state.Data_set.chang,
							artWidth: this.$store.state.Data_set.kuan,
							artHeight: this.$store.state.Data_set.gao
						},
						artRemark: this.$store.state.Data_set.Description_of_creation
					}
				}).then(function(response) {
					console.log(response)
					if(response.body.meta.res == '00000'){
						this.open3()
						this.$router.push({
						path: '../slider'
						});
						
						this.$store.state.img_box_to = []
						this.$store.state.Data_set = {}
						this.$store.state.img_box = {
							picUrl: 'static/img/xin/icon_announce_mainImg.png'
						}
					}
				}).catch(function(err) {
					console.log(err)
				})
				
				}else{
					Toast({
											message: '必须上传主图',
											position: 'middle',
											duration: 2000
										});
				}
			},
		},
		mounted(){
			this.biao = this.$store.state.Data_set.Type_work
			var type_ma = this.$store.state.type_ma
			if(type_ma == 5 || type_ma == 14 || type_ma == 17 || type_ma == 18 || type_ma == 21 || type_ma == 22) {
				this.acts.noa = false;
				this.acts.noe = true
			} else if(type_ma == 0) {
				this.acts.noa = true;
				this.acts.noe = false
			}
			this.btn2();
		}
	}
</script>

<style scoped="scoped">
	* {
		outline: none;
		margin: 0;
		padding: 0;
	}
	
	.boxs {
		width: 100%;
		height: 1rem;
		margin-top: 0.5rem;
		line-height: 1rem;
		border-bottom: 0.01rem solid #D2D2D2;
	}
	
	.btnss {
		width: 1.3rem;
		height: 0.5rem;
		float: right;
		border: none;
		border: 0.02rem solid #D2D2D2;
		border-radius: 0.15rem;
		font-size: 0.2rem;
		margin-top: 0.25rem;
		margin-left: 0.2rem;
		background: white;
		line-height: 0.5rem;
		color: #D2D2D2;
		position: relative;
	}
	
	.btnssto {
		width: 1.3rem;
		height: 0.5rem;
		float: right;
		border: none;
		border: 0.02rem solid #2C3E50;
		border-radius: 0.15rem;
		font-size: 0.2rem;
		margin-top: 0.25rem;
		margin-left: 0.2rem;
		color: #2C3E50;
		line-height: 0.5rem;
		background: white;
		position: relative;
	}
	
	.img_xiao {
		width: 0.3rem;
		height: 0.3rem;
		position: absolute;
		bottom: 0;
		right: 0;
		display: block;
	}
	
	.img_lang {
		display: none;
	}
	
	.type {
		display: none;
	}
	
	.types {
		display: block;
	}
	
	.zhu {
		width: 2.5rem;
		height: 2.5rem;
		float: left;
	}
</style>


