<template>
	<div style="width: 100%;height:13.33rem;position: relative;z-index: 9992;background: white;">
		<div style="width: 100%;height: 0.88rem;position: fixed;top:0;left: 0;z-index: 9992;line-height: 0.88rem;border-bottom: 0.005rem solid #999999;">
			<!--<img @click="hui" style="width: 0.4rem;height: 0.4rem;float:left;margin:0.25rem 0.3rem;" src="../../assets/img/back.png" alt="" />-->
	   		<img @click="hui" style="width: 0.4rem;height: 0.4rem;float:left;margin:0.25rem 0.3rem;" src="../../assets/img/zuo.png" alt="" />
	   		<p style="font-size: 0.36rem;margin-left: 3.1rem;">我的粉丝</p>
		</div>
		<div style="height: 0.88rem;"></div>
			
		<div class="chat_box" v-for="(item,index) in new_active_fs">
			<img class="imgs" style="" :src="item.opPic" alt="" />
			
			<div  @click="fensi(item)" class="text_box">
				
				<p style="font-size: 0.38rem;margin-top: 0.45rem;margin-left: 0.2rem;">{{item.nickname}}</p>
				<p class="ps1" style="width:70%;">{{item.signature}}222222222222222222</p>
				
				<div class="biaos" @click.stop="addAttention(item)">
					<img style="float: left;margin-left:0.2rem;margin-top: 0.16rem;" class="jia" src="../../assets/img/icon_my_add.png"/>
					<p style="font-size:0.26rem;float:left;text-align: center;">关注</p>
				</div>
			</div>
		</div>
		
		<div class="chat_box" v-for="item in new_my_friend">
			<img class="imgs" style="" :src="item.opPic" alt="" />
			
			<div @click="fensi(item)" class="text_box">
				
				<p style="font-size: 0.38rem;margin-top: 0.45rem;margin-left: 0.2rem;">{{item.nickname}}</p>
				<p class="ps1" style="font-size: 0.24rem;color: #B1B1B1;margin-left: 0.2rem;">{{item.signature}}</p>
				    <div class="biao">互为好友</div>
			</div>
		</div>
		
		
	</div>
</template>

<script>
	import store from '../../vuex/store.js'
	import md5 from 'js-md5';
	export default{
		store,
		data(){
			return{
			   active_fs:[],//旧粉丝
			   my_friend:[],//旧好友
			   new_active_fs:[],//新粉丝
			   new_my_friend:[],//新好友
			   attention:'关注'
			}
		},
		methods:{
			fensi(i){
			 
				
				
				this.$store.state.Author_s_detailed_routing = 4
			   this.$store.state.worksOpId = i.opId
//			   window.location = '#/personal_data'
			    this.$router.push({
						path: '../personal_data'
						});
			},
			
			
			hui(){
				
//				window.location ='#/myspace'
				this.$router.push({
						path: '../myspace'
						});
			},
			/*获取粉丝列表*/
			huoqu_fs(){
				var url="http://www.ding-new.com:8080/a10/api/map/user/artUserLists.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
				
				this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/user/artUserLists.do?memId="+id+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				emulateJSON: false,
				method: 'post',
				body: {
					opId:this.$store.state.data.memId,
					typeId:'2'
				}
			}).then(function(response) {
				
				if(response.body.meta.res == '00000'){
                    this.active_fs = response.body.data
                    console.log("___________粉丝_________________")
                    console.log(this.active_fs)
                    
                    
                    this.getFriend();
				}
			}).catch(function(err){
				console.log(err)
			})
			},
			/*获取好友列表*/
			getFriend(){
				var url="http://www.ding-new.com:8080/a10/api/map/user/artUserLists.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
				this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/user/artUserLists.do?memId="+id+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				emulateJSON: false,
				method: 'post',
				body: {
					opId:'1',
					typeId:'1'
				}
			}).then(function(response) {
				
				if(response.body.meta.res == '00000'){
                    this.my_friend = response.body.data
                    console.log("___________好友_________________")
                    console.log(this.my_friend)
                    
                    /*确保有好友数据和粉丝数据时，才找*/
                   if(this.active_fs.length>0){
                   	this.find_fs();//找粉丝
                    this.find_friend();//找好友
                   }
                    
				}
			}).catch(function(err){
				console.log(err)
			})
			},
			/*添加关注*/
			addAttention(item){
				console.log("进来");
				var url="http://www.ding-new.com:8080/a10/api/map/art/addAttention.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
				
				
				this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/art/addAttention.do?memId="+id+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				emulateJSON: false,
				method: 'post',
				body: {
					opId:'1',
					attentionOpId:item.opId,
					attentionType:'1'
				}
				}).then(function(response) {
					console.log(response.body)
				if(response.body.meta.res == '00000'||response.body.meta.res == '99999'){
                  
                    console.log("___________添加关注_________")
                    this.attention="已关注"
                    this.huoqu_fs()
                    
				}else{
					this.attention="已关注"
				}
			}).catch(function(err){
				console.log(err)
			})
			},
			/*判断是否在数组内*/
			find_tong(arr,str){
				
				for(var i = 0; i < arr.length; i++){
        			if(str === arr[i].opId){
            			return true;
        			}
    			}
    			return false;
			},
				/*找粉丝*/
			find_fs(){
				for(var i=0;i<this.active_fs.length;i++){
						//console.log(this.find_tong(this.my_friend,this.active_fs[i].opId))
						if(!this.find_tong(this.my_friend,this.active_fs[i].opId)){
							/*防止重复push*/
							if(!this.find_tong(this.new_active_fs,this.active_fs[i].opId)){
								this.new_active_fs.push(this.active_fs[i])
								console.log(this.active_fs[i].opId,"粉丝")
							}	
						}
				 }
			},
			/*找好友*/
			find_friend(){
				
				console.log(this.my_friend,"好友数组");
					for(var i=0;i<this.active_fs.length;i++){
						
						if(this.find_tong(this.my_friend,this.active_fs[i].opId)){
						/*防止重复push*/
							if(!this.find_tong(this.new_my_friend,this.active_fs[i].opId)){
								this.new_my_friend.push(this.active_fs[i])
								console.log(this.active_fs[i].opId,"好友")
							}	
						}
				    }
			    },
		    },
		
		created(){
			this.huoqu_fs();//获取粉丝列表
		},
		computed:{
		
		}
	}
</script>

<style scoped="scoped">
	.chat_box{
		width: 100%;
		height: 1.7rem;
		margin-top: 0.02rem;
	}
	.ps1{
		font-size: 0.24rem;color: #B1B1B1;margin-left: 0.2rem;
		 width: 70%;height: 0.3rem;
		
		 display: -webkit-box;  
                        display: -moz-box;  
                        overflow: hidden;  
                        text-overflow: ellipsis;  
                        word-break: break-all;  
                        -webkit-box-orient: vertical;  
                        -webkit-line-clamp:1;
	}
	.imgs{
		width: 1.3rem;height:1.3rem;
	   margin: 0.2rem;
	   border-radius: 50%;
	}
	.text_box{
		width: 5.78rem;
		height: 100%;
		border-bottom:0.005rem solid #2C3E50 ;
		float: right;
		position: relative;
	}
	.biao{
		font-size: 0.26rem;
		width: 1.3rem;
		height:0.54rem;
		border: 0.01rem solid #CACACA;
		text-align: center;
		line-height: 0.54rem;
		float: right;
		position: absolute;
		right: 0.2rem;
		bottom: 0.5rem;
		border-radius: 0.11rem;
		color: #CACACA;
	}
	.biaos{
		font-size: 0.26rem;
		width: 1.3rem;
		height:0.54rem;
		border: 0.01rem solid black;
		text-align: center;
		line-height: 0.54rem;
		float: right;
		position: absolute;
		right: 0.2rem;
		bottom: 0.5rem;
		border-radius: 0.11rem
	}
	.jia(){
		width: 0.22rem;
		height: 0.22rem;
		float: left;
		
	}
</style>


