<template>
	<div style="width: 100%;position: relative;z-index:300;background: white;">
		<div style="width: 100%;height: 0.88rem;position: fixed;top:0;left: 0;z-index: 300;line-height: 0.88rem;border-bottom: 0.005rem solid #999999;background: white;">
			<!--<img @click="hui" style="width: 0.4rem;height: 0.4rem;float:left;margin:0.25rem 0.3rem;" src="../../assets/img/back.png" alt="" />-->
	   		<img @click="hui" style="width: 0.4rem;height: 0.4rem;float:left;margin:0.25rem 0.3rem;" src="../../assets/img/zuo.png" alt="" />
	   		<p style="font-size: 0.36rem;margin-left: 3.1rem;">我的好友</p>
		</div>
		<div style="height: 0.88rem;"></div>
			
			
			
		<div class="chat_box" v-for="item in my_friend">
			<img class="imgs" style="" :src="item.opPic" alt="" />
			
			<div @click="tiao(item)" class="text_box">
				
				<p style="font-size: 0.38rem;margin-top: 0.45rem;margin-left: 0.2rem;">{{item.nickname}}</p>
				
				<p class="bs" style="-webkit-box-orient:vertical">{{item.signature}}</p>
				
				<div class="biao">互为好友</div>
			</div>
		</div>
		
		
	</div>
</template>

<script>
	import store from '../../vuex/store.js'
	import md5 from 'js-md5';
	export default{
		store,
		data(){
			return{
			
			   my_friend:[],//好友
			  
			}
		},
		methods:{
			tiao(i){
//				console.log(i,'详情')
//				this.$store.state.Author_s_detailed_routing = 5
//			    this.$store.state.worksOpId = i.opId
////			    window.location = '#/personal_data'
      
               this.$store.state.chat_tiao = 1
                this.$store.state.chat_item.fromOpId = i.opId
                this.$store.state.chat_item.fromNiceName = i.nickname
                
			    this.$router.push({
						path: '../chat_to'
						});
				
			},
			
			hui(){
				
				if(this.$store.state.adcal == 2){
					
					this.$router.push({
						path: '../myspace'
					});
					
				}else if(this.$store.state.adcal == 1){
					
					this.$router.push({
						path: '../message/chat'
					});
					
				}
//				window.location ='#/myspace'
			},
			/*获取好友列表*/
			getFriend(){
				var url="http://www.ding-new.com:8080/a10/api/map/user/artUserLists.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
		        
		        
				console.log("进来");
				this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/user/artUserLists.do?memId="+id+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				emulateJSON: false,
				method: 'post',
				body: {
					opId:this.$store.state.data.memId,
					typeId:'1'
				}
			}).then(function(response) {
				
				if(response.body.meta.res == '00000'){
                    this.my_friend = response.body.data
                    console.log("___________好友_________________")
                    console.log(this.my_friend)
				}
			}).catch(function(err){
				console.log(err)
			})
			},
			
		},
		mounted(){
				this.getFriend();//获取好友
			}
	}
</script>

<style scoped="scoped">
	.chat_box{
		width: 100%;
		height: 1.7rem;
		margin-top: 0.02rem;
		background: white;
		
	}
	.imgs{
		width: 1.3rem;height:1.3rem;
	   margin: 0.2rem;
	   border-radius: 50%;
	}
	.text_box{
		width: 5.78rem;
		height: 100%;
		border-bottom:0.005rem solid #2C3E50 ;
		float: right;
		position: relative;
	}
	.biao{
		font-size: 0.26rem;
		width: 1.3rem;
		height:0.54rem;
		border: 0.01rem solid #CACACA;
		text-align: center;
		line-height: 0.54rem;
		float: right;
		position: absolute;
		right: 0.2rem;
		bottom: 0.5rem;
		border-radius: 0.11rem;
		color: #CACACA;
	}
	.bs{
		font-size: 0.24rem;color: #B1B1B1;margin-left: 0.2rem;
		width: 4rem;
		overflow: hidden;
       text-overflow:ellipsis;
                        white-space: nowrap;
	}
</style>


