<template>
	<div id="app">
		<div class="head">
			<img src="../../assets/img/back.png" @touchstart="backTo" />
			<p>忘记密码</p>
		</div>
		<div class="phone">
			<p>手机</p>
			<input ref="phone_input" type="text" placeholder="请输入登陆手机号" maxlength="11" />
		</div>
		<div class="code">
			<p class="code_text">验证</p>
			<input ref="code_input" type="text" placeholder="请输入验证码" />
			<dt></dt>
			<p v-show="show" class="getCode" @touchstart="getCode">获取验证码</p>
			<p v-show="!show" :class="{time_blue:is_blue,time_red:is_red}">{{count}} s</p>

		</div>
		<div class="phone">
			<p>密码</p>
			<input ref="password_input" type="password" placeholder="请重新输入登录密码" />
		</div>

		<div class="sumbit" @touchstart="sumbit">
			<p>确认密码</p>
		</div>
	</div>
</template>

<script>
	import md5 from 'js-md5';
	import { Toast } from 'mint-ui';
	export default {
		data() {
			return {
				show: true,
				timer: null,
				count: '',
				is_red: false,
				is_blue: true,

			}
		},
		methods: {
			backTo() {
				this.$router.push({
					path: '../login'
				})
			},
			getCode() {
				var reg = /^1[3|4|5|7|8][0-9]{9}$/;
				var phone = this.$refs.phone_input.value.trim();
				if(reg.test(phone)) {

					/*验证码倒计时*/
					const TIME_COUNT = 30;
					if(!this.timer) {

						this.count = TIME_COUNT;
						this.show = false;
						this.timer = setInterval(() => {
							if(this.count > 0 && this.count <= TIME_COUNT) {
								if(this.count <= 10) {
									this.is_blue = false;
									this.is_red = true;
								} else {
									this.is_blue = true;
									this.is_red = false;
								}
								this.count--;
							} else {
								this.show = true;
								clearInterval(this.timer);
								this.timer = null;
							}
						}, 1000)
					}

					var params = "{\"memPhone\":\"" + phone + "\",\"msgType\":\"3\",\"msgLen\":\"4\"}";
					this.$http({
						url: 'http://www.ding-new.com:8080/a10/api/user/getSmsMsg.do',
						method: 'post',
						headers: {
							"content-type": "application/json;charset=UTF-8"
						},
						body: params,
						emulateJSON: false,
					}).then(function(response) {
						console.log(response);
					}).catch(function(err) {
						console.log(err)
					})
				} else {
					/*提示语*/
					Toast({
						message: '手机号格式不对',
						position: 'middle',
						duration: 2000
					});

				}
			},
			sumbit() {
				var reg = /^1[3|4|5|7|8][0-9]{9}$/;
				var reg_pass = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,12}$/; //密码正则，6-12位，包含一种数字一种字母
				var phone = this.$refs.phone_input.value.trim();
				var code = this.$refs.code_input.value.trim();
				var password = this.$refs.password_input.value.trim();
				if(phone != null && phone != "") {
					if(reg.test(phone)) {
						if(code != null && code != "") {
							if(password != null & password != "") {
								if(reg_pass.test(password)) {
									var m_pass = md5(password);
									console.log(phone + "  " + code + " " + password);
									this.$http({
										url: 'http://www.ding-new.com:8080/a10/api/user/forgetPassword.do',
										method: 'post',
										headers: {
											"content-type": "application/json;charset=UTF-8"
										},
										body: {
											loginAccount: phone,
											smsVerifCode: code,
											pwdNew: m_pass,
											pid: "684589bb5b0e40059ceb41d71be23d8d",
											rid: "631e32975df34e89a6ec9f58512b912f",
											cpFlag: "0"
										},
										emulateJSON: false,
									}).then(function(response) {
										console.log(response);
										if(response.body.meta.res == "00000") {
											Toast({
												message: '修改成功',
												position: 'middle',
												duration: 2000
											});
											this.$router.push({
												path: '../login'
											})
										}
									}).catch(function(err) {
										Toast({
											message: '修改失败，请重新修改',
											position: 'middle',
											duration: 2000
										});
									})
								} else {
									Toast({
										message: '6-12位,必须包含至少一个字母和一个数字',
										position: 'middle',
										duration: 2000
									});
								}
							} else {
								Toast({
									message: '密码不能为空',
									position: 'middle',
									duration: 2000
								});
							}

						} else { /*提示语*/
							Toast({
								message: '验证码不能为空',
								position: 'middle',
								duration: 2000
							});
						}

					} else {
						/*提示语*/
						Toast({
							message: '手机号格式不对',
							position: 'middle',
							duration: 2000
						});

					}
				} else {
					/*提示语*/
					Toast({
						message: '手机号不能为空',
						position: 'middle',
						duration: 2000
					});
				}
			}

		}
	}
</script>

<style scoped="scoped">
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "微软雅黑";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	
	input,
	textarea {
		outline: none;
		border: 0;
	}
	
	input::-webkit-input-placeholder {
		/* WebKit browsers */
		color: #D0D0D0;
		font-size: 0.32rem;
		line-height: 1.2rem;
	}
	
	input:-moz-placeholder {
		/* Mozilla Firefox 4 to 18 */
		color: #D0D0D0;
		font-size: 0.32rem;
		line-height: 1.2rem;
	}
	
	input::-moz-placeholder {
		/* Mozilla Firefox 19+ */
		color: #D0D0D0;
		font-size: 0.32rem;
		line-height: 1.2rem;
	}
	
	input:-ms-input-placeholder {
		/* Internet Explorer 10+ */
		color: #D0D0D0;
		font-size: 0.32rem;
		line-height: 1.2rem;
	}
	
	.head {
		margin-top: 0.3rem;
		height: 0.8rem;
		border-bottom: rgba(247, 247, 247, 1) solid 3px;
	}
	
	.head img {
		float: left;
		width: 0.5rem;
		margin-left: 0.3rem;
		height: 0.5rem;
	}
	
	.head p {
		font-size: 0.4rem;
		text-align: center;
	}
	
	.phone {
		height: 1rem;
		width: 90%;
		margin: 0.3rem 5%;
		border-bottom: rgba(247, 247, 247, 1) solid 3px;
	}
	
	.phone p {
		float: left;
	}
	
	.phone p {
		width: 30%;
		line-height: 0.8rem;
		font-size: 0.4rem;
	}
	
	.phone input {
		float: right;
		width: 70%;
		font-size: 0.32rem;
		height: 0.8rem;
	}
	
	.code {
		height: 1rem;
		width: 90%;
		margin: 0.3rem 5%;
		border-bottom: rgba(247, 247, 247, 1) solid 3px;
	}
	
	.code_text {
		float: left;
		width: 30%;
		line-height: 0.8rem;
		font-size: 0.4rem;
	}
	
	.code input {
		float: left;
		width: 40%;
		font-size: 0.32rem;
		height: 0.8rem;
	}
	
	.code dt {
		float: left;
		width: 0.03rem;
		background: rgba(46, 49, 53, 1);
		height: 0.6rem;
		margin-top: 0.2rem;
	}
	
	.getCode {
		float: left;
		width: 28%;
		text-align: center;
		line-height: 0.9rem;
		font-size: 0.3rem;
	}
	
	.sumbit {
		width: 80%;
		height: 1rem;
		border-radius: 0.1rem;
		background-color: #000000;
		position: absolute;
		top: 6rem;
		margin: 0 10%;
	}
	
	.sumbit p {
		text-align: center;
		color: #fff;
		font-size: 0.425rem;
		margin-top: 0.2rem;
	}
	
	.time_blue {
		float: left;
		width: 28%;
		text-align: center;
		line-height: 0.9rem;
		font-size: 0.3rem;
		color: green;
	}
	
	.time_red {
		float: left;
		width: 28%;
		text-align: center;
		line-height: 0.9rem;
		font-size: 0.3rem;
		color: red;
	}
	#app{
		width: 100%;
	height: 13.3rem;
	position: relative;
	z-index: 300;
	background: white;
	}
</style>


