<template>
	<!--
    	作者：2443611475@qq.com
    	时间：2018-03-21
    	描述：点击发布
    -->
	<div class="das" style="width: 100%;height: 13.3rem;position: absolute;top:0;z-index: 9993;;">
		<div style="width: 80%;height: 2rem;margin: 0 auto;margin-top: 1rem;position: relative;">
			<p style="font-size: 0.9rem;float:left;">22</p>
			<div style="margin-top: 0.25rem;margin-left: 0.5rem;float:left;">
				<p style="font-size: 0.3rem;">星期四</p>
				<p style="font-size: 0.3rem;">03/2018</p>
			</div><br />
			<p style="font-size:0.32rem;position: absolute;bottom: 0.3rem;left: 0;">请选择你需要发布的类型</p>
		</div>
		
		<div style="width: 100%;height: 2rem;position: relative;bottom: -6.5rem;left: 0;">
			
			<div @touchstart="to_add" class="anniu"><img class="imgs2" src="../../assets/img/QQs220180323150231.png" alt="" /></div>
			 <p style="font-size: 0.3rem;position: absolute;bottom: 0rem;left: 0.8rem;">作品</p>
			
			<div @touchstart="toDiaries" class="anniu"><img class="imgs2" src="../../assets/img/QQs320180323150257.png" alt="" /></div>
			<p style="font-size: 0.3rem;position: absolute;bottom: 0rem;left: 2.5rem;">闲情</p>
			
			<div @touchstart="to_Publisharticles" class="anniu"><img class="imgs2" src="../../assets/img/QQs420180323150304.png" alt="" /></div>
			<p style="font-size: 0.3rem;position: absolute;bottom: 0rem;right: 2.6rem;">文章</p>
			
			<div @touchstart="to_Deeparticle" class="anniu"><img class="imgs2" src="../../assets/img/QQs420180323150304.png" alt="" /></div>
			<p style="font-size: 0.3rem;position: absolute;bottom: 0rem;right: 0.9rem;">深度</p>
			
		</div>
		
		<div style="width: 100%;height: 1rem;position: absolute;bottom: 0;border-top: 0.02rem solid #EDEDED;background:white;">
			<img @touchstart="hui" style="width: 0.5rem;height: 0.5rem;margin-left: 45%;margin-top: 0.2rem;" src="../../assets/img/QQxx20180323150929.jpg"/>
		</div>
		
	</div>
</template>

<script>
	import store from '../../vuex/store.js'
	export default{
		store,
//		props:['m'],
		methods:{
			hui(){
				if(this.$store.state.home_tai == 1){
					 
					 this.$router.push({
						path: '../slider'
						});
				}else if(this.$store.state.home_tai == 2){
					 this.$router.push({
						path: '../square'
						});
					
				}else if(this.$store.state.home_tai == 3){
					this.$router.push({
						path: '../message'
						});
					
				}else if(this.$store.state.home_tai == 4){
					this.$router.push({
						path: '../myspace'
						});
					
				}
				
				
				
               
//				this.m.ad = false
				
			},
			to_add(){
//				this.m.ad = false
				
				this.$router.push({
						path: '../add'
						});
					
			},
			toDiaries(){
//				this.m.ad = false
				
				this.$router.push({
						path: '../Diaries'
						});
					
			},
			to_Publisharticles(){
//				this.m.ad = false
				
				this.$router.push({
						path: '../Publisharticles'
						});
					
			},
			to_Deeparticle(){
//				this.m.ad = false
				
				this.$router.push({
						path: '../Deeparticle'
						});
					
			}
		},
	}
</script>

<style scoped="scoped">
	.anniu{
		width: 1.2rem;
		height: 1.2rem;
		background: #333333;
		float: left;
		margin-left: 0.53rem;
		margin-top: 0.3rem;
		border-radius: 50%;
	}
	.das{
		  background:rgba(255,255,255,0.95)
	}
	.imgs2{
		width: 0.51rem;
		height: 0.52rem;
		margin-left: 0.34rem;
		margin-bottom: 0.05rem;
		margin-top: 0.35rem;
	}
</style>


