<template>
	<div id="app">
		<div class="kong" v-show="!show">
			<img src="../../assets/img/icon_noArticle.png" />
		</div>
		<div v-show="show" class="works_item" v-for="(arr,index) in data" @click="toArticle(arr.artId)">
			<div class="xian" v-if="(arr.insertDate&&index>0)?true:false"></div>
			<div class="date" >
				<p class="day">{{arr.insertDate.split(' ')[0].split("-")[1]}}日</p>
				<p class="month">{{arr.insertDate.split(' ')[0].split("-")[2]}}月</p>
			</div>
			<div class="works_img">
				
				<div  class="big_img" style="vertical-align:middle;text-align:center;overflow:hidden;">
					<img style="max-height:auto;max-width:100%;" v-if="arr.picInfo.length>0" :src="arr.picInfo[0].picUrl"/>
				</div>
				
				<div class="small_img">
					<p class="title">{{arr.artTitle}}</p>
					<p class="content" >{{arr.artRemark}}</p>
					<div class="article_infor">
						<p v-if="arr.artFlag=='4'?true:false" class="shen">深度</p>
						<p v-if="arr.artFlag=='3'?true:false" class="ren">热点</p>
						<p style="text-align: right;" class="commtent">{{arr.readerDiscuss}}评论</p>
					</div>
				</div>
			</div>
			<!--<div class="down"></div>-->
		</div>
	</div>
</template>

<script>
	import store from '../../vuex/store.js'
	import md5 from 'js-md5';
	export default {
		//			el:"#app",
		store,
		data() {
			return {
				act: [],
				data: [],
				show: true,
				day: [],
				month: [],
				time:''
			}
		},
		methods: {
//			r() {
//				for(var i = 0; i < this.data.length; i++) {
//					this.month.push(this.data[i].insertDate.split(' ')[0].split("-")[1]);
//					
//				}
//				for(var i = 0; i < this.data.length; i++) {
//					
//					this.day.push(this.data[i].insertDate.split(' ')[0].split("-")[2]);
//				}
//			},
			toArticle(e){
				this.$store.state.back_id=2
				this.$store.state.worksId=e
				this.$store.state.worksName="文章"
				
				this.$router.push({
						path: '../works_detail'
						});
			}
		},
		mounted(){
//			console.log(this.$store.state.data.memId);
			var url="http://www.ding-new.com:8080/a10/api/map/art/selectFlag.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
			console.log(this.$store.state.worksOpId)
			this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/art/selectFlag.do?memId="+id+"&ts="+ts,
				method: 'post',
				headers: {
					"content-type":"application/json;charset=UTF-8",
					'sIgn': sign
				},
				body:{
					opId: this.$store.state.worksOpId,
					typeId: "3",
					page:1,
					rows:10
				},
				emulateJSON: false,
			}).then(function(response){
				
				if(response.body.meta.res == "00000"){
					this.data = response.body.data.artDocumentsList;
					
					console.log(this.data,"文章");
					
					if(this.data.length == 0){
						this.show = false
					}else{
						this.show = true
//						this.r();
					}
					
				}
			}).catch(function(err){
				console.log(err)
				this.show = false
			})
		}
	}
</script>

<style scoped="scoped">
	#app {
		overflow-x: hidden;
	}
	
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "微软雅黑";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	
	.works_item {
		width: 100%;
		margin: 0.3rem 0 0;
		overflow: hidden;
	}
	
	.date {
		width: 100%;
		height: 0.6rem;
		margin: 0.5rem 0.25rem 0;
	}
	
	.day {
		float: left;
		font-size: 0.45rem;
		font-weight: bold;
	}
	
	.month {
		float: left;
		font-size: 0.3rem;
		color: #000;
		font-weight: bold;
		margin-left: 0.1rem;
		line-height: 0.95rem;
	}
	
	.works_img {
		width: 100%;
		height: 2.2rem;
		margin-left: 0.2rem;
		margin-top: 0.3rem;
    	margin-bottom: 0.2rem;
	}
	
	.works_img img {
		/*width: 100%;
		height: 100%;*/
	}
	
	.small_img {
		float: left;
		width: 54%;
		height: 100%;
		margin-right: 0.3rem;
		margin-left: 1.5%;
	}
	
	.big_img {
		float: left;
		width: 40%;
		height: 100%;
		clear: both;
		background-color: #007AFF;
	}
	
	.title {
		font-size: 0.35rem;
	}
	
	.content {
		margin-top: 0.25rem;
		margin-right: 1.5%;
		height: 0.8rem;
		font-size: 0.25rem;
		display: -webkit-box ;
    	overflow: hidden;
    	text-overflow: ellipsis;
    	word-break: break-all; 
    	-webkit-line-clamp: 2;
	}
	
	.article_infor {
		margin-top: 0.25rem;
		width: 100%;
	}
	
	.shen {
		font-size: 0.25rem;
		color: #2AA4F2;
		float: left;
		padding: 0.01rem 0.07rem;
		border: #2AA4F2 0.02rem solid;
		border-radius: 0.08rem;
	}
	
	.ren {
		font-size: 0.25rem;
		color: #FD5858;
		float: left;
		padding: 0.005rem 0.07rem;
		border: #FD5858 0.02rem solid;
		border-radius: 0.08rem;
	}
	
	.commtent {
		font-size: 0.25rem;
		width: 45%;
		margin-right: 5%;
		color: #D0D0D0;
		float: right;
	}
	
	.xian {
		height: 0.2rem;
		width: 100%;
		background: rgba(247, 247, 247, 1);
	}
	
	.down {
		width: 100%;
		height: 0.01rem;
		background: rgba(247, 247, 247, 1);
	}
	
	.kong {
		height: 3.5rem;
		width: 100%;
	}
	
	.kong img {
		width: 3rem;
		height: 2.8rem;
		margin: 15% 30%;
	}
</style>


