<template>
	<div style="width: 100%;">
		<div  class="chat_box">
			<img class="imgs" style="" src="../../assets/img/xitong/icon_news_gifts.png" alt="" />
			<div @click="tiao" class="text_box">
				<p style="font-size: 0.38rem;margin-top: 0.6rem;margin-left: 0.2rem;">好友通知</p>
					<div class="fri_shu" v-if="$store.state.friends.length>0"></div>
				<i style="font-size: 0.45rem;color:#CACACA;position: absolute;right:0.2rem;top: 0.7rem;"class="fa fa-chevron-right"></i>
			</div>
		</div>
		
		<div class="chat_box" v-for="item in data">
			<!--用户头像-->
			<img class="imgs" style="" :src="item.fromOpPic" alt="" />
			
			<div @click="to_chat(item)" class="text_box">
				<!--{{item}}-->
				<!--用户ID-->
				<p style="font-size: 0.38rem;margin-top: 0.4rem;">{{item.fromNiceName}}</p>
				<!--消息-->
				<p class="text_p">{{item.message}}</p>
				<!--时间-->
				<p class="data_p">{{date}}</p>
			</div>
		</div>
		
		<div style="width: 100%;height: 1.5rem;">
			
		</div>
		
	</div>
</template>

<script>
	import store from '../../vuex/store'
	export default{
		store,
		data(){
			return{
//				data:''
			}
		},
		methods:{
			
			
			tiao(){
				this.$store.state.adcal = 1
				
				this.$router.push({
						path: '../My_friend'
						});
			},
			
			to_chat(i){
                 
                 this.$store.state.chat_tiao = 0
                 
                this.$store.state.chat_item = i
                
                this.$router.push({
						path: '../chat_to'
						});
			    
			},
		},
		created(){
				console.log(this.$store.state.msgs_to,"消息")
//				this.data = this.$store.state.msgs_to
				
		},
		computed:{
			date(){
				var date=new Date();
				return date.getHours()+":"+date.getMinutes();
			},
			
			data(){
				return this.$store.state.msgs_to
			}
		}
	}
</script>

<style scoped="scoped">
	.chat_box{
		width: 100%;
		height: 1.7rem;
	}
	.imgs{
		width: 1.3rem;height:1.3rem;
	   margin: 0.2rem;
	   border-radius: 50%;
	}
	.text_box{
		width: 5.78rem;
		height: 100%;
		border-bottom:0.005rem solid #2C3E50 ;
		float: right;
		position: relative;
	}
	.text_p{
		font-size: 0.3rem;
		width: 5rem;
		height: 0.4rem;
		overflow: hidden;
		text-overflow:ellipsis;
        white-space: nowrap;
		color: #CACACA;
	}
	.data_p{
		font-size: 0.28rem;
		position: absolute;
		right: 0.5rem;
		top: 0.4rem;
		color: #CACACA;
	}
	.fri_shu{
		
		padding: .15rem;
 position: fixed;
 left: 3.5rem;
 top: 1.5rem;
 border-radius: 50%;
 background-color: red;
	}
</style>


