<template>
	<div style="width: 100%;">
		
		<div @touchstart="fensi" class="chat_box">
			<img class="imgs" style="" src="../../assets/img/xitong/icon_news_fans.png" alt="" />
			<div class="text_box">
				<p style="font-size: 0.38rem;margin-top: 0.6rem;margin-left: 0.2rem;">新的粉丝</p>
				<i style="font-size: 0.45rem;color:#CACACA;position: absolute;right:0.2rem;top: 0.7rem;"class="fa fa-chevron-right"></i>
			</div>
		</div>
		
		<div @touchstart="Ctoncs" class="chat_box">
			<img class="imgs" style="" src="../../assets/img/xitong/icon_news_comment.png" alt="" />
			<div class="text_box">
				<p style="font-size: 0.38rem;margin-top: 0.6rem;margin-left: 0.2rem;">点赞、评论</p>
				<i style="font-size: 0.45rem;color:#CACACA;position: absolute;right:0.2rem;top: 0.7rem;"class="fa fa-chevron-right"></i>
			</div>
		</div>
		
		<div @touchstart="dashang" class="chat_box">
			<img class="imgs" style="" src="../../assets/img/xitong/icon_news_gifts.png" alt="" />
			<div class="text_box">
				<p style="font-size: 0.38rem;margin-top: 0.6rem;margin-left: 0.2rem;">打赏记录</p>
				<i style="font-size: 0.45rem;color:#CACACA;position: absolute;right:0.2rem;top: 0.7rem;"class="fa fa-chevron-right"></i>
			</div>
		</div>
		
		
		
	</div>
</template>

<script>
	export default{
		methods:{
			fensi(){
					this.$router.push({
						path: '../myfensi'
						});
				
			},
			Ctoncs(){
				this.$router.push({
						path: '../Ctoncs'
						});
				
			},
			dashang(){
				this.$router.push({
						path: '../dashang'
						});
				
			},
		}
	}
</script>

<style scoped="scoped">
	.chat_box{
		width: 100%;
		height: 1.7rem;
		margin-top: 0.02rem;
	}
	.imgs{
		width: 1.3rem;height:1.3rem;
	   margin: 0.2rem;
	   border-radius: 50%;
	}
	.text_box{
		width: 5.78rem;
		height: 100%;
		border-bottom:0.005rem solid #2C3E50 ;
		float: right;
		
		position: relative;
	}
	
</style>


