<template>
	<div id="app">
		<div class="back" @touchstart="backTo">
			<img src="../../assets/img/back.png" />
			<p>返回</p>
		</div>

		<div class="title">
			<div class="title_left">
				<p class="title_p">专业领域</p>
				<p class="title_tip">请选择您所在的专业领域，可多选</p>
			</div>
		</div>
		<div class="content">
			<div class="item_content">
				<ul class="item1">
					<li v-for="item in item_content">{{item}}</li>
				</ul>
			</div>
			<div class="tab">
				<ul>
					<li @touchstart="li1" :class="{lis:act.noa1,listo:act.noe1}">
						<p>视觉</p>
						<div :class="{move:act.noa1,defultTo:act.noe1}"></div>
					</li>
					<li @touchstart="li2" :class="{lis:act.noa2,listo:act.noe2}">
						<p>文学</p>
						<div :class="{move:act.noa2,defultTo:act.noe2}"></div>
					</li>
					<li @touchstart="li3" :class="{lis:act.noa3,listo:act.noe3}">
						<p>设计</p>
						<div :class="{move:act.noa3,defultTo:act.noe3}"></div>
					</li>
				</ul>
			</div>
			<div :class="{vanish:act.noa1,select_ul:act.noe1}">
				<ul class="eyes">
					<li v-for="(arr,index) in eye " @touchstart="eyes(index)">{{arr.name}}<img v-show="arr.id" src="../../assets/img/gou.png" /></li>

				</ul>
			</div>
			<div :class="{vanish:act.noa2,select_ul:act.noe2}">
				<ul class="letters">
					<li v-for="(lett,index) in letter" @touchstart="letters(index)">{{lett.name}}<img v-show="lett.id" src="../../assets/img/gou.png" /></li>

				</ul>
			</div>
			<div :class="{vanish:act.noa3,select_ul:act.noe3}">
				<ul class="designs">
					<li v-for="(des,index) in design" @touchstart="designs (index)">{{des.name}}<img v-show="des.id" src="../../assets/img/gou.png" /> </li>
				</ul>
			</div>
		</div>

		<div class="sumbit" @touchstart="confirm">
			<p>确认选择</p>
		</div>
	</div>
</template>

<script src="../../../static/js/adaption.js"></script>
<script src="../../../static/js/swiper.js"></script>

<script>
	import { Toast } from 'mint-ui';
	import store from '../../vuex/store.js'
	import md5 from 'js-md5';
	export default {
		//			el:"#app",
		store,
		data() {
			return {
				is_if: false,

				act: {
					noa1: false,
					noe1: true,
					noa2: true,
					noe2: false,
					noa3: true,
					noe3: false
				},
				eye: [{
						name: "国画",
						id: false
					},
					{
						name: "当代水墨",
						id: false
					},
					{
						name: "书法",
						id: false
					},
					{
						name: "篆刻",
						id: false
					},
					{
						name: "油画",
						id: false
					},
					{
						name: "版画",
						id: false
					},
					{
						name: "素描",
						id: false
					},
					{
						name: "水墨画",
						id: false
					},
					{
						name: "雕塑",
						id: false
					}
				],
				letter: [{
						name: "小说",
						id: false
					},
					{
						name: "戏剧",
						id: false
					},
					{
						name: "散文",
						id: false
					},
					{
						name: "剧本",
						id: false
					},
					{
						name: "寓言",
						id: false
					},
					{
						name: "诗体",
						id: false
					}

				],
				design: [{
						name: "环境设计",
						id: false
					},
					{
						name: "建筑设计",
						id: false
					},
					{
						name: "室内设计",
						id: false
					},
					{
						name: "展示设计",
						id: false
					},
					{
						name: "公共艺术设计",
						id: false
					},
					{
						name: "景观设计",
						id: false
					},
					{
						name: "工业设计",
						id: false
					},
					{
						name: "家具设计",
						id: false
					},
					{
						name: "交通工具设计",
						id: false
					}
				],
				item_content: [],//选择的子项数组
				item_title: '视觉'//默认选择的标题
			}

		},
		methods: {
			backTo() {
//				window.location = '/#/regiter_information'
                this.$router.push({path: '../regiter_information'})
			},
			confirm() {

				/*处理选择后的子项数组，格式跟后台保持一致*/
				if(this.item_content.length > 0) {
					console.log(this.item_title, "--", this.item_content)
					var str = ""
					if(this.item_content.length > 1) {
						for(var i = 0; i < this.item_content.length; i++) {
							if(i == this.item_content.length - 1) {
								str += this.item_content[i]
							} else {
								str += this.item_content[i] + ","
							}

						}

					} else {
						str = this.item_content[0]
					}
					
					/*上传信息*/
					if(str != "") {
//						console.log(str, "领域", this.$store.state.regiter_data, )
                        
						var url = this.$store.state.request_url + "/api/map/user/editArtist.do";
						var memId = localStorage.memId;
						var ts = new Date().getTime();
						var token = localStorage.tokEn;
						var sign = md5(url + memId + token + ts);
						this.$http({
							url: this.$store.state.request_url+'/api/map/user/editArtist.do?memId='+memId+"&ts="+ts,
							method: 'post',
							headers: {
								"content-type": "application/json;charset=UTF-8",
								"sIgn":sign
							},
							body: {
								opId: localStorage.memId, //用户Id
								nickname: this.$store.state.regiter_data.nink, //昵称
								opSex: this.$store.state.regiter_data.sex, //性别
								opBirthday: this.$store.state.regiter_data.both, //生日
								opAddress: { //地址
									na: "",
									ar: "",
									pc: "",
									pv: this.$store.state.regiter_data.location.split("-")[0],
									ct: this.$store.state.regiter_data.location.split("-")[1],
									dt: "",
									da: this.$store.state.regiter_data.location,
									fa: "",
									lo: "",
									la: ""
								},
								specialization: {//专业领域
									artCategoryName: this.item_title,
									artCategorySubclass: str
								},
								opPic: this.$store.state.regiter_data.img//头像路径
							},
							emulateJSON: false,
						}).then(function(response) {
							if(response.body.meta.res === "00000") {
								console.log("成功")
								
								localStorage.opPic=this.$store.state.regiter_data.img;
								localStorage.nickname=this.$store.state.regiter_data.nink;
								
								/*提示语*/
								Toast({
									essage: "注册成功",
									position: 'middle',
									duration: 2000
									});
									
									
								/*成功后跳转*/
//								window.location="/#/slider"
								   this.$router.push({path: '../slider'})
								
							}else{
								
								console.log(response.body.meta.msg)
								/*提示语*/
								Toast({
									essage: response.body.meta.msg,
									position: 'middle',
									duration: 2000
									});
							}
						})

					}

				} else {
					/*提示语*/
					Toast({
						message: "请选择专业领域",
						position: 'middle',
						duration: 2000
					});
				}

			},
			li1() {
				this.item_title = "视觉";
				this.act.noa1 = false;
				this.act.noe1 = true
				this.act.noa2 = true;
				this.act.noe2 = false
				this.act.noa3 = true;
				this.act.noe3 = false

			},
			li2() {
				this.item_title = "文学";
				this.act.noa1 = true;
				this.act.noe1 = false
				this.act.noa2 = false;
				this.act.noe2 = true
				this.act.noa3 = true;
				this.act.noe3 = false
			},
			li3() {
				this.item_title = "设计";
				this.act.noa1 = true;
				this.act.noe1 = false
				this.act.noa2 = true;
				this.act.noe2 = false
				this.act.noa3 = false;
				this.act.noe3 = true
			},
			/*视觉*/
			eyes(index) {

				for(var i = 0; i < this.letter.length; i++) {
					if(this.letter[i].id == true) {
						this.item_content = [];
					}
					this.letter[i].id = false;
				}

				for(var i = 0; i < this.design.length; i++) {
					if(this.design[i].id == true) {
						this.item_content = [];
					}
					this.design[i].id = false;
				}

				this.eye[index].id = !this.eye[index].id;
				if(this.eye[index].id == true) {
					if(this.item_content.indexOf(this.eye[index].name)) {
						if(this.item_content.length==5){
							/*提示语*/
								Toast({
									essage: "最多只能选择五个",
									position: 'middle',
									duration: 2000
									});
						}else{
							this.item_content.push(this.eye[index].name);
						}
						
					}

				}

				if(this.eye[index].id == false) {

					for(var i = 0; i < this.eye.length; i++) {

						if(this.item_content.indexOf(this.eye[index].name) > -1) {
							for(var j = 0; j < this.item_content.length; j++) {
								if(this.item_content[j] == this.eye[index].name) {
									console.log(this.item_content[j]);
									this.item_content.splice(j, 1);
								}
							}

						}
					}

				}

			},
			/*文学*/
			letters(index) {
				for(var i = 0; i < this.eye.length; i++) {
					if(this.eye[i].id == true) {
						this.item_content = [];
					}
					this.eye[i].id = false;
				}
				for(var i = 0; i < this.design.length; i++) {
					if(this.design[i].id == true) {
						this.item_content = [];
					}
					this.design[i].id = false;
				}

				this.letter[index].id = !this.letter[index].id;
				if(this.letter[index].id == true) {
					if(this.item_content.indexOf(this.design[index].name)) {
						this.item_content.push(this.letter[index].name);
					}
				}
				if(this.letter[index].id == false) {
					for(var i = 0; i < this.letter.length; i++) {

						if(this.item_content.indexOf(this.letter[index].name) > -1) {
							for(var j = 0; j < this.item_content.length; j++) {
								if(this.item_content[j] == this.letter[index].name) {
									console.log(this.item_content[j]);
									this.item_content.splice(j, 1);
								}
							}

						}
					}

				}
			},
			/*设计*/
			designs(index) {
				for(var i = 0; i < this.eye.length; i++) {
					if(this.eye[i].id == true) {
						this.item_content = [];
					}
					this.eye[i].id = false;
				}
				
				for(var i = 0; i < this.letter.length; i++) {
					if(this.letter[i].id == true) {
						this.item_content = [];
					}
					this.letter[i].id = false;
				}

				this.design[index].id = !this.design[index].id;
				if(this.design[index].id == true) {
					if(this.item_content.indexOf(this.design[index].name)) {
						this.item_content.push(this.design[index].name);
					}

				}
				
				if(this.design[index].id == false) {
					for(var i = 0; i < this.design.length; i++) {

						if(this.item_content.indexOf(this.design[index].name) > -1) {
							for(var j = 0; j < this.item_content.length; j++) {
								if(this.item_content[j] == this.design[index].name) {
									console.log(this.item_content[j]);
									this.item_content.splice(j, 1);
								}
							}

						}
					}
				}
			}
		}

	}
</script>

<style scoped="scoped">
	li {
		list-style-type: none;
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	/*返回*/
	#app{
		width: 100%;height: 13.3rem;
		position: relative;z-index: 300;
	    background: white;
	}
	.back {
		width: 1.8rem;
		height: 1rem;
		margin-left: 0.25rem;
	}	
	
	.back img {
		width: 0.5rem;
		height: 0.5rem;
		float: left;
		margin-top: 0.425rem
	}
	
	.back p {
		font-size: 0.4rem;
		margin-left: 0.1rem;
		margin-top: 0.4rem;
		float: left;
	}
	/*标题*/
	
	.title {
		width: 100%;
		height: 1.75rem;
		margin-top: 0.25rem;
		margin-left: 0.25rem;
	}
	
	.title_p {
		font-size: 0.8rem;
		font-weight: bold;
		color: rgba(51, 51, 51, 1);
		font-family: castellar;
	}
	
	.title_tip {
		font-size: 0.35rem;
		color: #D0D0D0;
	}
	/*tab菜单*/
	
	.tab {
		width: 90%;
		height: 1rem;
		margin: 0.25rem 5%;
	}
	
	.tab ul {
		width: 100%;
		height: 100%;
	}
	/*.tab ul li{
		float: left;
		font-size: 0.8rem;
		color: #D0D0D0;
		width: 15%;
		margin-right: 0.5rem;
	}*/
	
	.lis {
		float: left;
		font-size: 0.4rem;
		color: #D0D0D0;
		width: 15%;
		margin-right: 0.25rem;
	}
	
	.listo {
		float: left;
		font-size: 0.4rem;
		color: #000000;
		width: 15%;
		margin-right: 0.25rem;
		font-weight: 600;
	}
	
	.move {
		width: 70%;
		margin-left: 10%;
		margin-top: 0.15rem;
		height: 0.05rem;
	}
	
	.defultTo {
		width: 70%;
		margin-left: 10%;
		margin-top: 0.3rem;
		background-color: #000000;
		height: 0.05rem;
	}
	
	.sumbit {
		width: 80%;
		height: 0.75rem;
		border-radius: 0.1rem;
		background-color: #000000;
		position: fixed;
		bottom: 0.5rem;
		margin: 0 10%;
	}
	/*流式布局*/
	
	.select_ul {
		width: 90%;
		margin: 0 3%;
		display: block;
	}
	
	.select_ul ul {
		width: 100%;
		height: 1.75rem;
	}
	
	.select_ul ul li {
		font-size: 0.4rem;
		font-family: SimHei;
		color: rgba(46, 49, 53, 1);
		height: 0.6rem;
		text-align: center;
		float: left;
		border-radius: 0.15rem;
		border: #000 solid 1px;
		margin: 0.25rem 0.1rem;
		padding: 0 0.25rem;
		line-height: 0.6rem;
	}
	
	.vanish {
		width: 90%;
		margin: 0 3%;
		display: none;
	}
	
	.sumbit p {
		text-align: center;
		color: #fff;
		font-size: 0.375rem;
		margin-top: 0.1rem;
	}
	
	.eyes li,
	.letters li,
	.designs li {
		position: relative;
	}
	
	.eyes li img,
	.letters li img,
	.designs li img {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 0.3rem;
		height: 0.4rem;
	}
	
	.item_content {
		width: 100%;
	}
	
	.item1 {
		width: 100%;
		overflow: hidden;
	}
	
	.item1 li {
		font-size: 0.3rem;
		font-family: SimHei;
		color: rgba(46, 49, 53, 1);
		height: 0.5rem;
		text-align: center;
		float: left;
		border-radius: 0.15rem;
		border: #000 solid 1px;
		margin: 0.1rem 0.1rem;
		padding: 0 0.1rem;
		line-height: 0.5rem;
	}
	
	.content {
		width: 100%;
		height: 8rem;
		overflow-y: scroll;
	}
</style>


