							<div @click="li1" class="swiper-slide">
								<div @click="li1" :class="{lis:act.noa1,listo:act.noe1}">
									<p>资料</p>
									<div :class="{move:act.noa1,defultTo:act.noe1}"></div>
								</div>
							</div>
							<div @click="li2" class="swiper-slide">
								<div :class="{lis:act.noa2,listo:act.noe2}">
									<p>作品</p>
									<div :class="{move:act.noa2,defultTo:act.noe2}"></div>
								</div>
							</div>
							<div @click="li3" class="swiper-slide">
								<div :class="{lis:act.noa3,listo:act.noe3}">
									<p>闲情</p>
									<div :class="{move:act.noa3,defultTo:act.noe3}"></div>
								</div>
							</div>
							<div @click="li4" class="swiper-slide">
								<div :class="{lis:act.noa4,listo:act.noe4}">
									<p>文章</p>
									<div :class="{move:act.noa4,defultTo:act.noe4}"></div>
								</div>
							</div>
							<div @click="li5" class="swiper-slide">
								<div :class="{lis:act.noa5,listo:act.noe5}">
									<p>评论</p>
									<div :class="{move:act.noa5,defultTo:act.noe5}"></div>
								</div>
							</div>
							<div @click="li6" class="swiper-slide">
								<div :class="{lis:act.noa6,listo:act.noe6}">
									<p>相册</p>
									<div :class="{move:act.noa6,defultTo:act.noe6}"></div>
								</div>
							</div>
						</div>

					</div>

				</div>
				<datacontent v-show="act.noe1"></datacontent>
				<workscontent v-show="act.noe2"></workscontent>
				<diarycontent v-show="act.noe3"></diarycontent>
				<articlecontent v-show="act.noe4"></articlecontent>
				<commentbigcontent v-show="act.noe5"></commentbigcontent>
				<photoscontent v-show="act.noe6"></photoscontent>
				
				 <dashang v-show='$store.state.dashang' ></dashang>
			</div>
		</div>

		<div v-show="guanzhu" class="footer">
			<div  class="follw_btn" @click="followTap">
				<img src="../../assets/img/icon_detail_focous.png" />
				<p>{{reply_content}}</p>
			</div>
		</div>
		
		<commnetcontent v-show="$store.state.is_comment"></commnetcontent>
		
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	import datacontent from './data.vue'
	import workscontent from './works.vue'
	import diarycontent from './diary.vue'
	import articlecontent from './article.vue'
	import commentbigcontent from './comment.vue'
	import photoscontent from './photos.vue'
	import swiper from '../../../static/js/swiper.js'
	import store from '../../vuex/store.js'
	import commnetcontent from '../commnet_content.vue'
	import md5 from 'js-md5';
	import dashang from '../home/dashang.vue'
	
	export default {
		store,
		components: {
			//个人资料    作品      闲情      文章    相册    评论作品      评论
			datacontent,
			workscontent,
			diarycontent,
			articlecontent,
			photoscontent,
			commnetcontent,
			commentbigcontent,
			dashang,
		},
		data() {
			return {
				is_if: false,

				act: {
					noa1: false,
					noe1: true,
					noa2: true,
					noe2: false,
					noa3: true,
					noe3: false,
					noa4: true,
					noe4: false,
					noa5: true,
					noe5: false,
					noa6: true,
					noe6: false
				},
				reply_content: '申请关注',
                guanzhu:true,
			}

		},
		methods: {
			huis(){
				console.log(this.$store.state.Author_s_detailed_routing)
				
				if(this.$store.state.Author_s_detailed_routing == 1){
						
						this.$router.push({
						path: '../slider'
						});
				}else if(this.$store.state.Author_s_detailed_routing == 2){
					
					this.$router.push({
						path: '../square/Popularity_list'
						});
				}else if(this.$store.state.Author_s_detailed_routing == 3){
					this.$router.push({
						path: '../Mycollection/works'
						});
					//跳转至我的收藏
				}else if(this.$store.state.Author_s_detailed_routing == 4){
					
					this.$router.push({
						path: '../My_fans'
						});//跳转至我的粉丝
				}else if(this.$store.state.Author_s_detailed_routing == 5){
					
					this.$router.push({
						path: '../My_friend'
						});//跳转至我的好友列表
				}
				
			},
			backTo() {
				this.$router.push({
						path: '../regiter_information'
						});
				

			},
			confirm() {
				this.is_if = !this.is_if
				console.log(this.is_if)
			},
			li1() {
				

				this.act.noa1 = false;
				this.act.noe1 = true;
				this.act.noa2 = true;
				this.act.noe2 = false
				this.act.noa3 = true;
				this.act.noe3 = false
				this.act.noa4 = true;
				this.act.noe4 = false;
				this.act.noa5 = true;
				this.act.noe5 = false;
				this.act.noa6 = true;
				this.act.noe6 = false;
			},
			li2() {
				 /*作品*/
				this.act.noa1 = true;
				this.act.noe1 = false
				this.act.noa2 = false;
				this.act.noe2 = true
				this.act.noa3 = true;
				this.act.noe3 = false
				this.act.noa4 = true;
				this.act.noe4 = false
				this.act.noa5 = true;
				this.act.noe5 = false;
				this.act.noa6 = true;
				this.act.noe6 = false;
			},
			li3() {
				 /*日记*/
				this.act.noa1 = true;
				this.act.noe1 = false
				this.act.noa2 = true;
				this.act.noe2 = false
				this.act.noa3 = false;
				this.act.noe3 = true
				this.act.noa4 = true;
				this.act.noe4 = false
				this.act.noa5 = true;
				this.act.noe5 = false;
				this.act.noa6 = true;
				this.act.noe6 = false;
			},
			li4() {
				 /*文章*/
				this.act.noa1 = true;
				this.act.noe1 = false
				this.act.noa2 = true;
				this.act.noe2 = false
				this.act.noa3 = true;
				this.act.noe3 = false
				this.act.noa4 = false;
				this.act.noe4 = true
				this.act.noa5 = true;
				this.act.noe5 = false;
				this.act.noa6 = true;
				this.act.noe6 = false;
			},
			li5() {
			 /*评论*/
				this.act.noa1 = true;
				this.act.noe1 = false
				this.act.noa2 = true;
				this.act.noe2 = false
				this.act.noa3 = true;
				this.act.noe3 = false
				this.act.noa4 = true;
				this.act.noe4 = false
				this.act.noa5 = false;
				this.act.noe5 = true;
				this.act.noa6 = true;
				this.act.noe6 = false;
			},
			li6() {
				 /*相册*/
				this.act.noa1 = true;
				this.act.noe1 = false
				this.act.noa2 = true;
				this.act.noe2 = false
				this.act.noa3 = true;
				this.act.noe3 = false
				this.act.noa4 = true;
				this.act.noe4 = false
				this.act.noa5 = true;
				this.act.noe5 = false;
				this.act.noa6 = false;
				this.act.noe6 = true;
			},
			swipers() {
				new swiper('.swiper-container', {
					slidesPerView: 5,
					centeredSlides: false,
					
				})

			},
			followTap() {
				
				if(this.reply_content == "申请关注"){
					var url="http://www.ding-new.com:8080/a10/api/map/art/addAttention.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
					
					this.$http({
						url: "http://www.ding-new.com:8080/a10/api/map/art/addAttention.do?memId="+id+"&ts="+ts,
						method: 'post',
						headers: {
							"content-type": "application/json;charset=UTF-8",
							'sIgn': sign
						},
						body: {
							opId: this.$store.state.data.memId, //当前用户ID(储存的memId)
							attentionOpId:this.$store.state.worksOpId, //被关注用户Id(首页传入)
							attentionType: "1" //关注类型

						},
						emulateJSON: false,
					}).then(function(response) {
						console.log(response);
						if(response.body.meta.msg == "ok"||response.body.meta.msg == "已经关注了！") {
							
							
							this.reply_content = "取消关注"
						}
					}).catch(function(err) {
						console.log(err)
					})
				}else if(this.reply_content == "取消关注"){
					var url="http://www.ding-new.com:8080/a10/api/map/art/cancelAttention.do";
					var sign = md5(url+id+token+ts)
					
					this.$http({
						url: "http://www.ding-new.com:8080/a10/api/map/art/cancelAttention.do?memId="+id+"&ts="+ts,
						method: 'post',
						headers: {
							"content-type": "application/json;charset=UTF-8",
							'sIgn': sign
						},
						body: {
							opId: this.$store.state.data.memId, //当前用户ID(储存的memId)
							attentionOpId: this.$store.state.worksOpId //被关注用户Id(首页传入)
						

						},
						emulateJSON: false,
					}).then(function(response) {
						
						
						this.reply_content = "申请关注"
//						if(response.body.meta.msg == "已经关注了！") {
//							console.log("关注成功");
//							this.reply_content = "取消关注"
//						}
					}).catch(function(err) {
						console.log(err)
					})
				}

			},
         
         chaxun(){
         	var url="http://www.ding-new.com:8080/a10/api/map/art/attentionSuccessForPairOpId.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
         	
         	this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/art/attentionSuccessForPairOpId.do?memId="+id+"&ts="+ts,
				method: 'post',
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				body:{
					opId:this.$store.state.data.memId,//作者id
					attentionOpId:this.$store.state.worksOpId//被关注的Id
				},
				emulateJSON: false,
			}).then(function(response) {
				console.log(response.body,"关注查询");
				if(response.body.meta.msg == "NO") {
					this.reply_content = "申请关注"
//					this.$store.state.personal_c = response.body.data;
					//console.log(this.$store.state.personal_c.opPic);
				}else{
					this.reply_content = "取消关注"
				}
			}).catch(function(err) {
				console.log(err)
			})
         },
         
         followTap() {
				if(this.reply_content == "申请关注") {
					var url="http://www.ding-new.com:8080/a10/api/map/art/addAttention.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
					
					this.$http({
						url: "http://www.ding-new.com:8080/a10/api/map/art/addAttention.do?memId="+id+"&ts="+ts,
						method: 'post',
						headers: {
							"content-type": "application/json;charset=UTF-8",
							'sIgn': sign
						},
						body: {
							opId: this.$store.state.data.memId, //当前用户ID(储存的memId)
							attentionOpId:this.$store.state.worksOpId, //被关注用户Id(首页传入)
							attentionType: "1" //关注类型

						},
						emulateJSON: false,
					}).then(function(response) {
						console.log(response);
						if(response.body.meta.msg == "ok"||response.body.meta.msg == "已经关注了！") {
							console.log("关注成功");
							this.reply_content = "取消关注"
							Toast({
											message: "关注成功",
											position: 'middle',
											duration: 2000
										});
						}
					}).catch(function(err) {
						console.log(err)
					})
				}else if(this.reply_content == "取消关注"){
					var url1="http://www.ding-new.com:8080/a10/api/map/art/cancelAttention.do";
					var sign1 = md5(url1+id+token+ts)
					
					this.$http({
						url: "http://www.ding-new.com:8080/a10/api/map/art/cancelAttention.do?memId="+id+"&ts="+ts,
						method: 'post',
						headers: {
							"content-type": "application/json;charset=UTF-8",
							'sIgn': sign1
						},
						body: {
							opId:this.$store.state.data.memId, //当前用户ID(储存的memId)
							attentionOpId: this.$store.state.worksOpId //被关注用户Id(首页传入)
						},
						emulateJSON: false,
					}).then(function(response) {
						
						Toast({
											message: "取消关注",
											position: 'middle',
											duration: 0
										});
						this.reply_content = "申请关注"
//						if(response.body.meta.msg == "已经关注了！") {
//							console.log("关注成功");
//							this.reply_content = "取消关注"
//						}
					}).catch(function(err) {
						console.log(err)
					})
				}

			}
         
		},
		mounted() {
			
			
			console.log(this.$store.state.worksOpId,'作者id')
			if(this.$store.state.worksOpId == this.$store.state.data.memId){
				this.guanzhu = false
			}
			
			this.swipers();
			console.log(this.$store.state.worksOpId);
			var url="http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
			
			this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do?memId="+id+"&ts="+ts,
				method: 'post',
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				body: {
					opId:this.$store.state.worksOpId//作者id
				},
				emulateJSON: false,
			}).then(function(response) {
				console.log(response.body.data);
				if(response.body.meta.res == "00000") {

					this.$store.state.personal_c = response.body.data;
					//console.log(this.$store.state.personal_c.opPic);
				}
			}).catch(function(err) {
				console.log(err)
			})
			 this.chaxun()
		}
       
	}
</script>

