	.follw_btn {
		width: 1.5rem;
		height: 1.4rem;
		margin: 0.2rem auto;
	}
	
	.follw_btn img {
		width: 0.9rem;
		height: 0.9rem;
		margin: 0 auto;
		border-radius: 50%;
	}
	
	.follw_btn p {
		font-size: 0.3rem;
		font-family: SimHei;
		color: rgba(51, 51, 51, 1);
		line-height: 0.335rem;
	}
	
	.big_content {
		width: 100%;
		height: 13.3rem;
		position: relative;
	}
	
	.content {
		width: 100%;
		position: absolute;
		top: 3.2rem;
		background-color: #fff;
		z-index: 5;
	}
	
	.xian {
		height: 0.2rem;
		width: 100%;
		background: rgba(247, 247, 247, 1);
	}
</style>


