<template>
	<div id="app">
		
		
		<div class="kong" v-show="!show">
				<img src="../../assets/img/icon_noArticle.png" />
		</div>
		<div v-show="show" class="works_item" v-for="(arr,index) in data" >
			
			<div @click="toDiary(index)">
			
			<div class="xian" v-if="(arr.insertDate&&index>0)?true:false"></div>
			<div class="date" >
				<p class="day">{{arr.insertDate.split(' ')[0].split("-")[1]}}日</p>
				<p class="month">{{arr.insertDate.split(' ')[0].split("-")[2]}}月</p>
			</div>
			<div class="works_img">
				
				<!--2-->
				<div class="two" style="height: 5rem;" v-if="arr.picInfo.length==2" >
					<div class="two_left" style="vertical-align:middle;text-align:center;overflow:hidden;">
						<img :src="arr.picInfo[0].picUrl"  style="height:100%;max-width:auto;"/>
					</div>
					
					<div class="two_right" style="height:5rem;vertical-align:middle;text-align:center;overflow:hidden;">
						<img :src="arr.picInfo[1].picUrl"  style="height:100%;max-width:auto;"/>
					</div>
					
					
				</div>
				
				<!--3-->
				<div class="three" style="height: 5rem;" v-else-if="arr.picInfo.length==3">
					<div  class="big_img" style="vertical-align:middle;text-align:center;overflow:hidden;">
						<img :src="arr.picInfo[0]?arr.picInfo[0].picUrl:''" style="height:100%;max-width:auto;"/>
					</div>
					<div class="small_img">
						<div class="small_img_01" style="vertical-align:middle;text-align:center;overflow:hidden;">
							<img :src="arr.picInfo[1]?arr.picInfo[1].picUrl:''" style="height:100%;max-width:auto;"/>
						</div>
						<div class="small_img_02" style="vertical-align:middle;text-align:center;overflow:hidden;">
							<img :src="arr.picInfo[2]?arr.picInfo[2].picUrl:''" style="height:100%;max-width:auto;"/>
						</div>
					</div>
				</div>
				
				<!--1-->
				<div v-else-if="arr.picInfo.length==1" style="vertical-align:middle;text-align:center;overflow:hidden;width: 100%;"> 
					
					<img :src="arr.picInfo[0].picUrl"  style="max-height:auto;max-width:100%;" />
					
				</div>
				

			</div>
			<div class="works_content">
				<p class="introduce_content">{{arr.docUrl}}</p>
			</div>
			<div class="down"></div>
			
			</div>
			
			<div class="function">
				<div class="function_item_left" @click.stop="goodTap(arr)">
					<div v-for="c in callStauts">
						<img v-if="c.id==arr.artId&&c.stauts=='1'" src="../../../static/img/icon_detail_good_normal.png" />
						<img v-else-if="c.id==arr.artId&&c.stauts=='2'" src="../../../static/img/icon_detail_good_active.png" />
					</div>
					<p>点赞</p>
				</div>
				<div class="function_item_left" @click.stop="collectTap(arr)">
					<div v-for="cl in collectStauts">
						<img v-if="cl.id==arr.artId&&cl.stauts=='1'" src="../../../static/img/icon_detail_collect_normal.png" />
						<img v-else-if="cl.id==arr.artId&&cl.stauts=='2'" src="../../../static/img/icon_detail_collect_active.png" />

					</div>
					<p>收藏</p>
				</div>
				<div class="function_item_left">
					<img src="../../assets/img/icon_detail_share.png" />
					<p>转发</p>
				</div>
				<div class="function_item_left" @click.stop="commentTap(index)">
					<img src="../../assets/img/icon_detail_comment.png" />
					<p>评论</p>
				</div>
				<div @click="dashang(arr)" class="function_item_right">
					<img src="../../assets/img/icon_detail_tip.png" />
					<p>打赏</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import store from '../../vuex/store.js'
	import { Toast } from 'mint-ui';
	import md5 from 'js-md5';
	
	export default {
		//			el:"#app",
		components:{
			
		},
		store,
		data() {
			return {
				act: [{
						name: '11'
					},
					{
						name: '12'
					},
					{
						name: '13'
					},
					{
						name: '14'
					}
				],
				
				op:"p",
				
				data:[],
				show:true,
				clo:"p",
				day:[],
				month:[],
				time:'',
				
				callStauts: [], //储存点赞状态
				call: {
					id: '',
					stauts: ''
				},
				collectStauts: [], //储存收藏状态
				collect: {
					id: '',
					stauts: ''
				},
				
				
			}

		},
		methods: {
			dashang(i){
				this.$store.state.A_reward = i
				
				this.$store.state.A_reward.artFlag = '2'
				
				this.$store.state.dashang = true
				
				this.$store.state.Return_to_return = 3
			},
			
			toDiary(index) {
				this.$store.state.worksName="闲情";
				this.$store.state.back_id = 2
				
				this.$store.state.worksId = this.data[index].artId;
				
//				console.log(this.$store.state.worksId,'——————闲情')
				
				
				this.$router.push({
						path: '../works_detail'
						});
			},
			goodTap(index) { //点赞事件
//				console.log(index, "点赞作品")
                var url="http://www.ding-new.com:8080/a10/api/map/art/artLike.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
				this.$http({
					url: "http://www.ding-new.com:8080/a10/api/map/art/artLike.do?memId="+id+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						'sIgn': sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId: index.opId, //作者Id
						likeType: "1", //类型
						artId: index.artId, //作品Id
						likeOpId: this.$store.state.data.memId //用户Id
					}
				}).then(function(response) {
					console.log(response.body)
					this.callStauts = [];
					if(response.body.meta.res == "00000") {
						for(var i = 0; i < this.data.length; i++) {
//							console.log(this.data[i])
							this.likes(this.data[i]);
						}
						Toast({
								message: '点赞成功',
								position: 'middle',
								duration: 2000
							});
					} else if(response.body.meta.res == "99999") {
						Toast({
							message: '你已经点赞了该作品',
							position: 'middle',
							duration: 2000
						});
					}

				}).catch(function(err) {
					console.log(err)
				})

			},
			collectTap(index){
				var url="http://www.ding-new.com:8080/a10/api/map/art/artCollect.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
				
				this.$http({
					url: "http://www.ding-new.com:8080/a10/api/map/art/artCollect.do?memId="+id+"&ts="+ts,
					headers: {
						"content-type": "application/json;charset=UTF-8",
						'sIgn': sign
					},
					emulateJSON: false,
					method: 'post',
					body: {
						opId:this.$store.state.data.memId, //用户Id
						collectType: "1", //类型
						artId: index.artId, //作品Id
						collectOpId: index.opId //作者Id
					}
				}).then(function(response) {
//					console.log(response.body)
					this.collectStauts = [];

					if(response.body.meta.res=="00000"||response.body.meta.res=="99999"){
						for(var i = 0; i < this.data.length; i++) {
//						console.log(this.data[i])
//						console.log("_______收藏________")
						 this.search_collect(this.data[i])
					 }
					}
					

				}).catch(function(err) {
					console.log(err)
				})
			},
			commentTap(index){
//				console.log("触发");
				this.$store.state.commentWorksdata=this.data[index];
				this.$store.state.is_comment=true;
			},
			r(){
//			   for(var i=0;i<this.data.length;i++){
//			   	   this.month.push(this.data[i].insertDate.split(' ')[0].split("-")[1]);
//			     }
//			   for(var i=0;i<this.data.length;i++){
//			   	   this.day.push(this.data[i].insertDate.split(' ')[0].split("-")[2]);
//			     }
			},
			
			likes(i) {
				var url="http://www.ding-new.com:8080/a10/api/map/art/likeForOpId.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
//				console.log(i, "作品", this.$store.state.data.memId)
				this.$http({
					url: "http://www.ding-new.com:8080/a10/api/map/art/likeForOpId.do?memId="+id+"&ts="+ts,
					method: 'post',
					headers: {
						"content-type": "application/json;charset=UTF-8",
						'sIgn': sign
					},
					body: {
						opId: this.$store.state.data.memId, //当前登录系统的会员ID
						attentionOpId: i.opId, //被点赞会员的ID
						artId: i.artId //被点赞的作品ID
					},
					emulateJSON: false,
				}).then(function(response) {
//					console.log(response.body.meta.msg, "状态")
					if(response.body.meta.res == '00000') {
						if(response.body.meta.msg == "NO") {
							this.call.id = i.artId;
							this.call.stauts = "1";
							this.callStauts.push(this.call); //未点赞
							this.call = {};
						} else if(response.body.meta.msg == "YES") {
							this.call.id = i.artId;
							this.call.stauts = "2";
							this.callStauts.push(this.call); //已点赞
							this.call = {};
						}
					}

//					console.log(this.callStauts)
				}).catch(function(err) {
					console.log(err)
				})

			},
			search_collect(i) {
				var url="http://www.ding-new.com:8080/a10/api/map/art/collectForOpId.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
				
				this.$http({
					url: "http://www.ding-new.com:8080/a10/api/map/art/collectForOpId.do?memId="+id+"&ts="+ts,
					method: 'post',
					headers: {
						"content-type": "application/json;charset=UTF-8",
						'sIgn': sign
					},
					body: {
						opId: this.$store.state.data.memId,
						attentionOpId: i.opId,
						artId: i.artId
					},
					emulateJSON: false,
				}).then(function(response) {
					if(response.body.meta.res == '00000') {
//						console.log(response.body.meta.msg, "收藏状态");
						if(response.body.meta.msg == "NO") {
							this.collect.id = i.artId;
							this.collect.stauts = "1";
							this.collectStauts.push(this.collect); //未收藏
							this.collect = {};
						} else if(response.body.meta.msg == "YES") {
							this.collect.id = i.artId;
							this.collect.stauts = "2";
							this.collectStauts.push(this.collect); //已收藏
							this.collect = {};
						}
					}
				}).catch(function(err) {
					console.log(err)
				})
			
		},
			
		huoqus(){//获取页面数据
			var url="http://www.ding-new.com:8080/a10/api/map/art/selectFlag.do";
		        var id=this.$store.state.data.memId;
		        var token=this.$store.state.data.tokEn;
		        var ts=new Date().getTime();
		        var sign = md5(url+id+token+ts)
			
			console.log(this.$store.state.worksOpId,"闲情-----------------------------------");
			this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/art/selectFlag.do?memId="+id+"&ts="+ts,
				method: 'post',
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				body: {
					opId: this.$store.state.worksOpId,
					typeId: "2",
					page:1,
					rows:10
				},
				emulateJSON: false,
			}).then(function(response){
				if(response.body.meta.res == "00000") {
					this.data = response.body.data.artDocumentsList;
					console.log(this.data,'闲情')
					   
			    if(this.data.length>0){
						this.show=true
						this.r();
						for(var i=0;i<this.data.length;i++){
							this.likes(this.data[i])
							
						}
						for(var i=0;i<this.data.length;i++){
							this.search_collect(this.data[i])
						 }
				  }else{
						this.show=false
			    }
				}
			}).catch(function(err) {
				console.log(err)
			})
			
			
			
		}
			
		},
		mounted() {
			this.huoqus()
           
           
            
           
		}

	}
</script>

<style scoped="scoped">
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "微软雅黑";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	
	.works_item {
		width: 100%;
		margin: 0.5rem 0;
	}
	
	.date {
		width: 100%;
		height: 0.6rem;
		margin: 0.5rem 0.25rem 0;
	}
	
	.day {
		float: left;
		font-size: 0.4rem;
		font-weight: bold;
	}
	
	.month {
		float: left;
		font-size: 0.25rem;
		color: #000;
		margin-left: 0.1rem;
		line-height: 0.75rem;
	}
	
	.works_img {
		width: 100%;
		/*height: 4rem;*/
	}
	
	.small_img {
		float: left;
		width: 39.5%;
		height: 100%;
		margin-left: 0.5%;
	}
	
	.big_img {
		float: left;
		width: 60%;
		height: 100%;
		clear: both;
		background-color: #007AFF;
	}
	
	.small_img_01 {
		width: 100%;
		height: 49.5%;
		background-color: #D0D0D0;
	}
	
	.small_img_02 {
		width: 100%;
		height: 49.5%;
		margin-top: 1%;
		background-color: #2C3E50;
	}
	
	/*.big_img img,*/
	/*.small_img_01 img,
	.small_img_02 img {
		width: 100%;
		height: 100%;
	}*/
	
	.works_content {
		margin-top: 0.1rem;
		width: 100%;
		overflow: hidden;
		margin-left: 0.1rem;
	}
	
	.works_content_title {
		font-size: 0.35rem;
		color: rgba(51, 51, 51, 1);
		line-height: 0.165rem;
	}
	
	.introduce {
		margin-top: 0.25rem;
		font-size: 0.215rem;
		color: rgba(51, 51, 51, 1);
		line-height: 0.165rem;
	}
	
	.introduce_content {
		margin-top: 0.25rem;
		font-size: 0.25rem;
		margin-right: 0.2rem;
		color: rgba(153, 153, 153, 1);
		line-height: 0.335rem;
	}
	
	.function {
		margin-top: 0.25rem;
		width: 100%;
		height: 0.75rem;
	}
	
	.function_item_left {
		float: left;
		width: 15%;
		text-align: center;
		height: 100%;
	}
	
	.function_item_left img,
	.function_item_right img {
		width: 50%;
		height: 0.5rem;
	}
	
	.function_item_left p,
	.function_item_right p {
		font-size: 0.215rem;
		color: rgba(153, 153, 153, 1);
		line-height: 0.55rem;
	}
	
	.function_item_right {
		float: right;
		width: 15%;
		margin-right: 0.05rem;
		text-align: center;
		height: 100%;
	}
	
	.down {
		width: 100%;
		height: 0.01rem;
		background: rgba(247, 247, 247, 1);
		margin-top: 0.3rem;
	}
	
	.xian {
		height: 0.2rem;
		width: 100%;
		background: rgba(247, 247, 247, 1);
	}
	
	/*.big_img img,*/
	/*.small_img_01 img,
	.small_img_02 img {
		width: 100%;
		height: 100%;
	}*/
	
	.two{
		width: 100%;
		overflow: hidden;
	}
	.three {
		width: 100%;
		height: 100%;
		float: left;
	}
	
	.two_left {
		width: 49.7%;
		height: 100%;
		margin-right: 0.3%;
		float: left;
	}
	
	.two_right {
		width: 49.7%;
		margin-left: 0.3%;
		height: 100%;
		float: right;
	}
	.kong{
		height: 5rem;
		width: 100%;
		
	}
	.kong img{
		width: 3rem;
		height:2.8rem;
		margin: 15% 30%;
	}
</style>


