<template>
	<div id="content">
		<div class="home_img">
			<img src="../../assets/img/home_img.png" />
		</div>
		<button class="login" @touchstart="login">
			<p >登录账号</p>
		</button>
		<button class="regiter" @touchstart="regiter">
			<p>注册账号</p>
		</button>
		<div class="webChat"  @touchstart="toChat">
			<dt></dt>	<p>您也可以用以下方式登录</p> <dt></dt>
		</div>
		<div class="webchat_img" @touchstart="bingPhoneTap">
				<img src="../../assets/img/webchat_img.png" />
				<p>微信</p>
		</div>
	</div>
</template>

<script>
	
	export default {
		
		methods: {
			login() {
//					window.location = '/#/login'
				this.$router.push({path: '../login'})
                   
			},
			regiter() {
				this.$router.push({
					path: '../regiter'
				})

			},
			bingPhoneTap() {
//				window.location = '/#/bing_phone'
					this.$router.push({path: '../bing_phone'})

			},
			changepassword(){
//				this.$router.push({
//					path: '../Modify_the_password'
//				})
//				window.location = '/#/Modify_the_password'
                this.$router.push({path: '../Modify_the_password'})
			},
			toChat(){
				this.$router.push({
					path: '../chat'
				})
			}
	
			
		}
	}
</script>

<style scoped="scoped">
	
	
	.home_img {
		width: 100%;
		height: 8rem;
	}
	
	.home_img img {
		width: 100%;
		height: 100%;
	}
	
	.login {
		width: 80%;
		height: 1rem;
		margin: 5% 10%;
		border-radius: 0.1rem;
		background-color: #fff;
		border: #000000 solid 5px;
	}
	
	.regiter {
		width: 80%;
		height: 1rem;
		margin: 1% 10%;
		border-radius: 0.1rem;
		background-color: #000000;
	}
	
	.login p {
		color: #000000;
		line-height: 1rem;
		font-size: 0.37rem;
	}
	
	.regiter p {
		color: #FFFFFF;
		line-height: 1rem;
		font-size: 0.37rem;
	}
	
	.webChat {
		margin-left: 10%;
		width: 80%;
		height: 0.4rem;
		margin-top: 0.2rem;
		text-align: center;
	}
	
	.webChat p {
		font-size: 0.3rem;
		color: #D0D0D0;
		
	}
	.webChat dt,.webChat p{
		float: left;
	}
	.webChat dt{
		width: 15%;
		height: 0.01rem;
		margin:0.2rem 0.2rem 0 ;
		
		background-color:#D0D0D0;
	}
	.webchat_img{
		width: 2rem;
		margin: 0.1rem auto;
		text-align: center;
		
	}
	.webchat_img img{
		width: 1rem;
		height: 1rem;
	}
	.webchat_img p{
		font-size: 0.3rem;
	}
	#content{
	width: 100%;
	height: 13.3rem;
	position: relative;
	z-index: 300;
	background: white;
	}
</style>


