<template>
	<div id="app" v-show="f.is_report">
		<div  @click="cancel" style="width: 100%;height: 18.7rem;position:fixed;top: 0;left: 0;background:rgba(0,0,0,0.4);z-index: 10;" >
		
		</div>
		<div class="dia">
		  
			<p class="report_class">举报</p>
			<div class="xian" ></div>
			<p @click="cancel" class="cancel">取消</p>
		</div>
	</div>
	
</template>

<script>
	export default {
		
		data() {
			return {
					
			}
		},
        props:['f'],
		methods: {
			cancel(){
				this.f.is_report = false
			},
			reply(){
				
			}
			
		}
	}
</script>
</script>

<style scoped="scoped">
	input,textarea{
		outline: none;
	}
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "微软雅黑";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	.dia{
		position:fixed;
		bottom: 0;
		z-index: 10;
		height: 2rem;
		background-color: #fff;
		width: 100%;
	}
	.report_class{
		height: 40%;
		line-height: 1rem;
		color:rgba(51,51,51,1);
		font-size: 0.35rem;
		text-align: center;
	}
	.cancel{
		height: 30%;
		line-height: 1.1rem;
		color:rgba(51,51,51,1);
		font-size: 0.35rem;
		text-align: center;
	}
	
	.down{
		width: 100%;
		height: 0.01rem;
		margin-top: 0.2rem;
		background:rgba(247,247,247,1);
	}
	.xian{
		height: 0.2rem;
		width: 100%;
		background:rgba(247,247,247,1);
	}
</style>


