<template>
	<div style="width: 100%;height: 13.33rem;position: relative;z-index: 300;">
       
       <div style="width: 100%;height: 13.33rem;position:absolute;z-index: 300;">
		<div class="swiper-container">
			<div class="swiper-wrapper">
				<div style="" class="swiper-slide">
					<img style="width:100%;height:100%;float:left;" src="../../static/img/wx1.png" alt="" />
				</div>
				
				<div style="" class="swiper-slide">
					<img style="width:100%;height:100%;float:left;" src="../../static/img/wx2.png" alt="" />
				</div>
				
				<div  @touchmove='tiao'  style="" class="swiper-slide">
					<img style="width:100%;height:100%;float:left;" src="../../static/img/wx3.png" alt="" />
				</div>
                         
			</div>
			<!-- 如果需要分页器 -->
			<div class="swiper-pagination"></div>
		</div>
       </div>

	</div>
</template>

<script>
	import store from '../vuex/store.js'
	import Swiper from '../../static/js/swiper.js'
	export default {
		store,
		methods: {
			swipers() {
				new Swiper('.swiper-container', {
//					direction: 'vertical',
					 
					 
					// 如果需要分页器
					pagination: {
						el: '.swiper-pagination',
					},

					
				})
			},
			
			tiao(event){
				let ev = ev||event
				if(ev.touches[0].clientX<200){
//					window.location = '/#/home'
					localStorage.yidao = 1
					
					this.$store.state.yidao = localStorage.yidao
					
					this.$router.push({
						path: '../home'
						});
				}
				
			}
		},
		mounted() {
            this.swipers()
		},
	}
</script>

<style scoped="scoped">
	@import url("../assets/css/swiper.css");
	.swiper-slide{
		width: 100%;
		height: 13.34rem;
	}
</style>


