<template>
	<div class="home">
		<!--<addfacth v-bind:m='me'></addfacth>-->
		
			
		<!--</div>-->
		   <router-view></router-view>
		
		<div class="bottom_div">
			<div :class="{spans:zuan.noe,spanstower:zuan.noa}"  style="font-size: 0.6rem;" @touchstart="one">
				<img class="imgs" :src="ims.img1"><p style="font-size:0.26rem;margin-top: -0.1rem;">首页</p></img>
			</div>
			
			<div :class="{spans:zuan.noe2,spanstower:zuan.noa2}" style="font-size: 0.6rem;" @touchstart="tower">
				<img class="imgs" :src="ims.img2"><p style="font-size:0.26rem;margin-top: -0.12rem;">广场</p></img></div>
			
			<div class='spanstower' style="font-size: 0.6rem;" @touchstart="therr">
				<img style="width: 0.8rem;height: 0.8rem;margin-top:0.2rem;margin-left:-0.1rem;" class="imgs" :src="ims.img3"></img></div>
			
			<div :class="{spans:zuan.noe4,spanstower:zuan.noa4}" style="font-size: 0.6rem;position: relative;" @touchstart="four">
				
					<div class="shu" v-show="$store.state.is_infro"></div>
				
				<img class="imgs" :src="ims.img4"><p style="font-size: 0.26rem;margin-top:-0.12rem">消息</p></img></div>
			
			<div :class="{spans:zuan.noe5,spanstower:zuan.noa5}" style="font-size: 0.6rem;" @touchstart="five">
				<img  class="imgs" :src="ims.img5"><p style="font-size: 0.26rem;margin-top: -0.12rem">我的</p></img></div>
		</div>
	</div>
</template>
<script>
	
	import router from '../../router/index.js'
	import addfacth from '../add/add_facth.vue'
	import store from '../../vuex/store.js'
	export default{
		store,
		components: {
//            addfacth,
              
               },
		  data(){
		  	return{
		  		zuan:{noa:false,noe:true, noa2:false,noe2:true, noa3:false,noe3:true, noa4:false,noe4:true, noa5:false,noe5:true,},
		  		
		  		me:{ad:false},
		  		
		  		ims:{
		  			img1:'static/img/xin/icon_home_normal.png',img1s:'static/img/xin/icon_home_active.png',
		  			img2:'static/img/xin/icon_ground_normal.png',img2s:'static/img/xin/icon_ground_active.png',
		  			img3:'static/img/xin/icon_announce.png',img3s:'static/img/xin/icon_announce_add.png',
		  			img4:'static/img/xin/icon_news_normal.png',img4s:'static/img/xin/icon_news_active.png',
		  			img5:'static/img/xin/icon_my_normal.png',img5s:'static/img/xin/icon_my_active.png',
		  		},
		  		is_show:true
		  		
		  	}
		  },
		  methods:{
		  	 one(){
                router.push({path:'../slider'})
                this.zuan.noa = true;this.zuan.noe = false;
                this.zuan.noe2 = true;this.zuan.noe3 = true;
                this.zuan.noe4 = true;this.zuan.noe5 = true;
                this.zuan.noa2 = false;this.zuan.noa3 = false;
                this.zuan.noa4 = false; this.zuan.noa5 = false;
                this.$store.state.tr = 0
                this.$store.state.home_tai = 1
                
                this.ims.img1 = 'static/img/xin/icon_home_active.png'
                this.ims.img2 ='static/img/xin/icon_ground_normal.png'
                this.ims.img3='static/img/xin/icon_announce.png'
                this.ims.img4='static/img/xin/icon_news_normal.png'
                this.ims.img5='static/img/xin/icon_my_normal.png'
                
		  	 },
		  	 tower(){
		  	 	router.push({path:'../square'})
		  	 	this.zuan.noa = false;this.zuan.noe = true;
		  	 	this.zuan.noe2 =false;this.zuan.noa2= true;
		  	 	this.zuan.noe3 = true;this.zuan.noe4 = true; 
		  	 	this.zuan.noe5 = true;this.zuan.noa5 = false;
		  	 	this.zuan.noa3 = false;this.zuan.noa4 = false;
		  	 	this.$store.state.tr = 0
		  	 	this.$store.state.home_tai = 2
		  	 	
		  	 	this.ims.img1 = 'static/img/xin/icon_home_normal.png'
                this.ims.img2 ='static/img/xin/icon_ground_active.png'
                this.ims.img3='static/img/xin/icon_announce.png'
                this.ims.img4='static/img/xin/icon_news_normal.png'
                this.ims.img5='static/img/xin/icon_my_normal.png'
		  	 	
		  	 },
		  	 therr(){

                router.push({path:'../add_facth'})
            
		  	 },
		  	 four(){
		  	 	this.$store.state.is_infro=false;
		  	 	router.push({path:'../message/chat'})
		  	 	this.zuan.noa = false;this.zuan.noe = true;
                this.zuan.noe2 = true;this.zuan.noe3 = true;
                this.zuan.noe4 = false;this.zuan.noe5 = true;
                this.zuan.noa2 = false;this.zuan.noa3 = false;
                this.zuan.noa4 = true; this.zuan.noa5 = false;
		  	 	this.$store.state.tr = 0
		  	 	this.$store.state.home_tai = 3
		  	 	
		  	 	 this.ims.img1 = 'static/img/xin/icon_home_normal.png'
                this.ims.img2 ='static/img/xin/icon_ground_normal.png'
                this.ims.img3='static/img/xin/icon_announce.png'
                this.ims.img4='static/img/xin/icon_news_active.png'
                this.ims.img5='static/img/xin/icon_my_normal.png'
		  	 	
		  	 },
		  	 five(){
		  	 	router.push({path:'../myspace'})
		  	 	this.zuan.noa = false;this.zuan.noe = true;
                this.zuan.noe2 = true;this.zuan.noe3 = true;
                this.zuan.noe4 = true;this.zuan.noe5 = false;
                this.zuan.noa2 = false;this.zuan.noa3 = false;
                this.zuan.noa4 = false; this.zuan.noa5 = true;
                this.$store.state.tr = 0
                this.$store.state.home_tai = 4
                
                 this.ims.img1 = 'static/img/xin/icon_home_normal.png'
                this.ims.img2 ='static/img/xin/icon_ground_normal.png'
                this.ims.img3='static/img/xin/icon_announce.png'
                this.ims.img4='static/img/xin/icon_news_normal.png'
                this.ims.img5='static/img/xin/icon_my_active.png'
                
		  	 }
		  },
		  mounted(){
		  	
		  	  	this.one()
		  	  	
               
		  }
	}
</script>

<style scoped="scoped">
	*{margin: 0;padding: 0;}
	
	.bottom_div{
		width: 100%;
		height: 1.3rem;
		background:rgba(255,255,255,1);
	    box-shadow:0px 0px  15px 0.1px #CACACA;
	    position: fixed;
	    bottom: 0rem;
	    left: 0;
	}
	
	
	.spans{
		margin-left: 0.5rem;
		color: #CACACA;
		position: relative;
		 transition: 1s;
		 width: 0.8rem;
		 height: 100%;
		 float: left;
		 margin-left: 0.65rem;
		 margin-top: 0.1rem;
	}
	.spanstower{
		margin-left: 0.5rem;
		color:black;
		position: relative;
		transition: 0.5s;
		width: 0.8rem;
		height: 100%;
		float: left;
		margin-left: 0.65rem;
		margin-top: 0.05rem;
		
	}
	.imgs{
		width: 0.45rem;
		height: 0.45rem;
		margin-bottom: -0.05rem;
		margin-left: 0.03rem;
	}
	.shu{
		padding: 0.1rem;
		position: absolute;
		top: 0.1rem;
		right: 0.2rem;
		background-color: red;
		border-radius: 50%;
	}
	.home{
		width: 100%;
		/*overflow: hidden;*/
		height: 13.25rem;
		position: relative;
		/*padding-top: 0.5rem;*/
	}
</style>


